import { users } from "../seeds/users";
import { toast } from "react-toastify";

const UserService = {
  signIn: async (email: string, password: string) => {
    if (email === "admin@as.com" && password === "admin") {
      return users[0];
    } else if (email === "user@as.com" && password === "user") {
      return users[1];
    } 
  },
};

export { UserService };
