
import React, { useState } from "react";
import styles from "./personal-page.module.scss";
import { Input, Button, List, Dropdown } from "semantic-ui-react";
import { TableContainer, TableHead } from "@material-ui/core";
import { Table, TableRow, TableCell, TableBody } from "semantic-ui-react";
import { toast } from 'react-toastify';
import classNames from 'classnames';
import moment from 'moment';
import Select from 'react-select';
import { inject, observer } from "mobx-react";

interface IAdminDashboardProps {}

const userOptions = [{ key: "a", value: "user@as.com", text: `${"user@as.com".toUpperCase()}` }];

const AdminDashboard = inject('packageStore', 'userStore', 'commonStore')(
  observer(
    class AdminDashboard extends React.Component<any> {

      state = {
        activeUser: {value: 1, email: 'admin@gmail.com', profileId: 0},
        activeProfileTransactions: [],
        newBalance: 0
      }

      componentWillMount() {
        this.props.userStore.loadProfilesAndUsers();
        this.props.packageStore.loadMyTransactions();
      }

      handleUserChange = (newUser:any) => {
        this.props.packageStore.setActiveUserId(newUser.value);
        this.setState({activeUser: newUser, activeProfileTransactions: this.props.packageStore.transactionsByActiveUserId});
      }

      getTransactionIdentifier = (transaction:any) => {
        if(transaction.paymentSystem.includes('Cryptocurrency'))
        {
          if(+transaction.sender === +this.props.packageStore.activeUserIdInAdmin)
          {
            return 'Покупка крипты';
          } else {
            return 'Продажа крипты';
          }
        } else {
          if(+transaction.sender === +this.props.packageStore.activeUserIdInAdmin)
          {
            if(String(transaction.recipient) === String(this.props.commonStore.appName))
            {
              return 'Пополнение счета'
            } else {
              return 'Покупка депозита'
            }
          }
          if(+transaction.recipient === +this.props.packageStore.activeUserIdInAdmin)
          {
            if(String(transaction.sender) === String(this.props.commonStore.appName))
            {
              return 'Снятие с  счета'
            } else {
              return 'Завершение депозита'
            }
          }
        }
        
      }
      

      render() {
        const { profilesandusers } = this.props.userStore;
        const { activeCurrencyMultiplier } = this.props.commonStore;
        // const { submitTransaction, cancelTransaction } = this.props.packageStore;
        const userOptions = profilesandusers.map((profileanduser:any) => {
          return {value: profileanduser.user.id, label: profileanduser.user.email, email: profileanduser.user.email, profileId: profileanduser.profile.id}
        });

        const activeProfile = this.props.userStore.getUserProfileById(this.state.activeUser.profileId);

        // console.log(this.props.userStore.getUserProfileById(this.state.activeUser.profileId));
        return (
          <div className={classNames(styles["main-content"])} style={{overflow: 'scroll'}}>
            {/* <List >
            {profiles.map((profile:any) => {
              return (
              <List.Item>{profile.id}</List.Item>
              );
            })}
            </List> */}
            <Select options={userOptions} value={this.state.activeUser} onChange={this.handleUserChange}/>
            Новый баланс <Input value={this.state.newBalance} onChange={(e:any) => this.setState({newBalance: e.target.value})}/><button onClick={() => this.props.userStore.updateProfileBalance(this.state.activeUser.profileId, this.state.activeUser.email, this.state.newBalance)}>Обновить баланс</button>
            {typeof activeProfile !== 'undefined'  && (
              <React.Fragment>
                <div>Баланс: {(activeProfile.profile.money * activeCurrencyMultiplier).toFixed(2)}{this.props.commonStore.activeCurrency}</div>
                <div>Пароль: {activeProfile.profile.password}</div>
                {/* <div>Логин: {activeProfile.profile.money}</div>
                <div>Email: {activeProfile.profile.money}</div> */}
              </React.Fragment>
            )}
            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Тип</TableCell>
                    <TableCell>Отправитель</TableCell>
                    <TableCell>Получатель</TableCell>
                    <TableCell>Дата</TableCell>
                    <TableCell>Сумма</TableCell>
                    <TableCell>Платежная система</TableCell>
                    <TableCell>Номер транзакции</TableCell>
                    <TableCell>Статус</TableCell>
                    <TableCell>Действия</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {this.props.packageStore.transactionsByActiveUserId.map((transaction:any) => {
                      if(this.props.income)
                      {
                        if(!(String(transaction.recipient) === String(this.props.userStore.currentUser.id)) || transaction.paymentSystem === this.props.commonStore.appName)
                        {
                          return (<React.Fragment></React.Fragment>);
                        }
                      }
                      if(this.props.outcome)
                      {
                        if(!(String(transaction.sender) === String(this.props.userStore.currentUser.id)) || transaction.paymentSystem === this.props.commonStore.appName)
                        {
                          return (<React.Fragment></React.Fragment>);
                        }
                      }
                    return (
                      <TableRow key={transaction.id}>
                      <TableCell align="right">{this.getTransactionIdentifier(transaction)}</TableCell>
                        <TableCell component="th" scope="row">
                          {String(transaction.sender) === String(this.props.userStore.currentUser.id) ? this.props.userStore.currentUser.username : transaction.sender}
                        </TableCell>
                        <TableCell component="th" scope="row">
                          {String(transaction.recipient) === String(this.props.userStore.currentUser.id) ? this.props.userStore.currentUser.username : transaction.recipient}
                        </TableCell>
                        <TableCell align="right">{moment(transaction.date).format('YYYY-MM-DD HH:mm:ss')}</TableCell>
                        <TableCell align="right">{(+transaction.amount * (transaction.paymentSystem.includes('Cryptocurrency') ? +activeCurrencyMultiplier : +activeCurrencyMultiplier)).toFixed(2)}{transaction.paymentSystem.includes('Cryptocurrency') ? this.props.commonStore.activeCurrency : this.props.commonStore.activeCurrency}</TableCell>
                        <TableCell align="right">{transaction.paymentSystem}</TableCell>
                        <TableCell align="right">{419925 + transaction.id}</TableCell>
                        <TableCell align="right" className={transaction.status === 'Обработано' ? classNames(styles.success) : transaction.status === 'Отклонено' ? classNames(styles.danger) : classNames(styles.warning)}>
                          {transaction.status}
                        </TableCell>
                        <TableCell align="right">
                          <button onClick={() => this.props.packageStore.submitTransaction(transaction.id)}>Обработать</button>
                          <button onClick={() => this.props.packageStore.cancelTransaction(transaction.id)}>Отклонить</button>
                          <button onClick={() => this.props.packageStore.waitTransaction(transaction.id)}>В ожидании</button>
                        </TableCell>
                      </TableRow>
                    );
                  })}
                </TableBody>
              </Table>
            </TableContainer>
          </div>
        );
      }
    }
  )
)
export { AdminDashboard };
