import React from "react";
import styles from "./depositList.module.scss";
import classNames from "classnames";
import { TableContainer, TableHead } from "@material-ui/core";
import { Table, TableRow, TableCell, TableBody } from "semantic-ui-react";
import { inject, observer } from "mobx-react";
import moment from 'moment';

interface IWithdrawProps {}

const DepositList = inject('packageStore', 'commonStore')(
  observer(
    class DepositList extends React.Component<any> {

      componentWillMount() {
        this.props.packageStore.loadPackages();
      }

      countTodayProfit = (deposit:any) => {
        const oneDay = 24 * 60 * 60 * 1000;
        const today = new Date(new Date().getTime() + 0 * oneDay);
        // const today = new Date();
        const depositStartDate = new Date(deposit.startDate);
        const depositEndDate = new Date(deposit.endDate);
        const daysFromToday = Math.round(Math.abs((+today - +depositStartDate) / oneDay));
        const depositTerm = Math.round(Math.abs((+depositEndDate - +depositStartDate) / oneDay));
        return deposit.capital * deposit.dailyProfit * Math.min(daysFromToday, depositTerm) / 100;
      };

      isFinished = (deposit:any) => {
        const oneDay = 24 * 60 * 60 * 1000;
        const today = new Date(new Date().getTime() + 0 * oneDay);
        // const today = new Date();
        const depositStartDate = new Date(deposit.startDate);
        const depositEndDate = new Date(deposit.endDate);
        const daysFromToday = Math.round(Math.abs((+today - +depositStartDate) / oneDay));
        const depositTerm = Math.round(Math.abs((+depositEndDate - +depositStartDate) / oneDay));
        // alert(daysFromToday + ' ' + depositTerm);
        return daysFromToday >= depositTerm;
      }

      render() {
        const { deposits } = this.props.packageStore;
        const { activeCurrencyMultiplier } = this.props.commonStore;
        return (
          <div className={classNames(styles.wrapper)}>
            <h1 className={classNames(styles.title)}>Общий объем инвестиций:</h1>
            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Депозит</TableCell>
                    <TableCell>Дата</TableCell>
                    <TableCell>Сумма</TableCell>
                    <TableCell>Заработано</TableCell>
                    <TableCell>Статус</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {deposits.map((deposit:any) => {
                    // if(this.isFinished(deposit))
                      // if(deposit.status === 1)
                      //   this.props.packageStore.finishPackage(deposit);
                    return (
                      <TableRow key={deposit.id}>
                        <TableCell component="th" scope="row">
                          {deposit.name}
                        </TableCell>
                        <TableCell align="right">{moment(deposit.startDate).format('YYYY-MM-DD HH:mm:ss')}</TableCell>
                        <TableCell align="right">{(deposit.capital * activeCurrencyMultiplier).toFixed(2)}{this.props.commonStore.activeCurrency}</TableCell>
                        <TableCell align="right">{(this.countTodayProfit(deposit) * activeCurrencyMultiplier).toFixed(2)}{this.props.commonStore.activeCurrency}</TableCell>
                        <TableCell align="right" className={this.isFinished(deposit) ? classNames(styles.success) : classNames(styles.warning)}>
                          {this.isFinished(deposit) ? 'Завершен' : 'В работе'}
                        </TableCell>
                      </TableRow>
                    );
                  })}
                  
                </TableBody>
              </Table>
            </TableContainer>
          </div>
        );
      }
    }
  )
);

export { DepositList };
