import React from "react";
import styles from "./vertical-sidebar.module.scss";
import { Sidebar, Menu, Icon } from "semantic-ui-react";
import { SemanticICONS } from "semantic-ui-react/dist/commonjs/generic";
import { NavLink } from "react-router-dom";
import classNames from 'classnames';

interface IVerticalSidebarProps {
  token: string;
  visible: boolean;
  toggleSidebar: Function;
}

const VerticalSidebar: React.FC<IVerticalSidebarProps> = ({ visible, toggleSidebar, token }) => {
  if (!token) return <></>;
  return (
    <Sidebar vertical as={Menu} animation="push" direction="left" visible width={!visible ? "thin" : "very thin"} style={{borderRight: "1px solid rgb(234, 234, 234)", boxShadow: "unset", paddingTop: "55px", overflow: "hidden", borderLeft: "none"}}>
      {MENU_ITEMS.map(({ route, icon, text }, index) => {
        return (
          <NavLink to={route} activeClassName={classNames(styles["active-item"])} key={index} className={classNames(styles["item"])}>
            <Menu.Item
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                height: "49px",
                borderBottom: "1px solid rgb(234, 234, 234)",
                textTransform: "uppercase",
                fontWeight: 600,
                fontSize: "13.3px"
              }}
            >
              {!visible ? <span>{text}</span> : ""}
              <Icon name={icon} style={!visible ? {fontSize: "1.2em"} : { margin: "0 auto", fontSize: "1.2em"}} />
            </Menu.Item>
          </NavLink>
        );
      })}
      <Menu.Item
        as="div"
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <div>
          <Icon
            name={!visible ? "arrow alternate circle left outline" : "arrow alternate circle right outline"}
            size={!visible ? "large" : "large"}
            onClick={() => toggleSidebar()}
            style={{
              color: "#2f80ed",
              width: "55px",
              margin: 0,
            }}
          />
        </div>
      </Menu.Item>
    </Sidebar>
  );
};

interface IMenuItems {
  route: string;
  icon: SemanticICONS;
  text?: string;
}

const MENU_ITEMS: IMenuItems[] = [
  {
    route: "/personal-page",
    icon: "list layout",
    text: "Главная",
  },
  {
    route: "/wallet",
    icon: "money bill alternate outline",
    text: "Кошелек",
  },
  {
    route: "/deposits",
    icon: "briefcase",
    text: "Депозиты",
  },
  {
    route: "/chart",
    icon: "chart line",
    text: "График",
  },
];

export { VerticalSidebar };
