import React, { useReducer, useState } from "react";
import styles from "../../components/deposits/deposit.module.scss";
import classNames from "classnames";
import Slider from "@material-ui/core/Slider";
import { Tooltip } from "@material-ui/core";
import { inject, observer } from "mobx-react";

interface ISliderSumProps {
  step: number;
  max: number;
  min: number;
  sumToShow: Function;
}

interface IHandleProps {
  children: React.ReactElement;
  open: boolean;
  value: number;
}

const SliderSum = inject('commonStore')(
  observer(
    class SliderSum extends React.Component<any> {

      ValueLabelComponent = (props: IHandleProps) => {
        const { children, open, value } = props;
    
        this.props.sumToShow((value * this.props.commonStore.activeCurrencyMultiplier).toFixed(2));
    
        return (
          <Tooltip open={open} title="">
            {children}
          </Tooltip>
        );
      };

      render() {
        const { activeCurrencyMultiplier } = this.props.commonStore;
        const defaultValue = (this.props.min * activeCurrencyMultiplier).toFixed(2);
        const step = (this.props.step * activeCurrencyMultiplier).toFixed(2);
        return (
          <div className={classNames(styles.slider)}>
            <Slider
              min={this.props.min}
              max={this.props.max}
              step={this.props.step}
              defaultValue={this.props.min}
              ValueLabelComponent={this.ValueLabelComponent}
              onChange={() => this.ValueLabelComponent}
            />
          </div>
        );
      }

    }
  )
);
