import React from "react";
import styles from "./cards.module.scss";
import classNames from 'classnames';
import { PaymentSystemCard } from "../payment-system-card";
import { IPaymentVariations } from '../../../../plans/payment-variants';

interface ICardsProps {
  paymentVariants: IPaymentVariations[];
  amount: number;
}


const Cards: React.FC<ICardsProps> = ({paymentVariants, amount}) => {

  return (
    <div className={classNames(styles["cards-container"])}>
      {paymentVariants.map((variant, index) => {
        return <PaymentSystemCard key={index} paymentVariant={variant} amount={amount}/>;
      })}
    </div>
  );
};

export { Cards };
