import React from "react";
import styles from "../../wallet-recharge.module.scss";
import classNames from "classnames";
import { Cards } from "../cards";
import { Input, Icon } from "semantic-ui-react";
import { PAYMENT_VARIATIONS } from "../../../../plans/payment-variants";
import { inject, observer } from "mobx-react";

interface IWalletContentProps {}

const WalletContent = inject('commonStore', 'authStore')(
  observer(
    class WalletContent extends React.Component<any> {

      state = {
        amount: 0
      }

      handleAmountChage = (e:any) => this.setState({amount: e.target.value});

      render() {
        return (
          <div className={classNames(styles.content)} style={this.props.commonStore.sidebarVisible ? {paddingRight: 100} : {}}>
            <div className={classNames(styles["payment-block"])}>
              <div className={classNames(styles.title)}>
                <h1>Пополнение баланса</h1>
              </div>
              <div className={classNames(styles["payment-settings"])}>
                <div>
                  <h3>Укажите сумму пополнения и валюту:</h3>
                </div>
                <div className={classNames(styles["input-summ"])}>
                  <Input focus={false} placeholder="Сумма" value={this.state.amount === 0 ? null : this.state.amount} onChange={this.handleAmountChage}/>
                </div>
                <div className={classNames(styles.currency)}>
                  {/* <Icon name="dollar" className={classNames(styles.icon)} /> */}
                  <Icon name={'dollar'} className={classNames(styles.icon)} />
                </div>
              </div>
            </div>
            <div className={classNames(styles.cards)}>
              <h4>Выберите способ оплаты:</h4>
              <Cards paymentVariants={PAYMENT_VARIATIONS} amount={this.state.amount}/>
            </div>
          </div>
        );
      }
    }
  )
)

export { WalletContent };
