import React, { useState } from "react";
import classNames from "classnames";
import styles from "../../header.module.scss";
import { Link } from "react-router-dom";
import { Icon, Dropdown, Menu } from "semantic-ui-react";
import UserProfileDropdown from "./user-profile-dropdown";
import { getBalance } from "../../../personal-page";
import UserCurrencyDropdown from './user-currency-dropdown';
import { inject, observer } from "mobx-react";




const SignInned = inject('userStore', 'commonStore')(
  observer(
      class SignInned extends React.Component<any> {
        
        render() {
          const { currentUserProfile } = this.props.userStore;
          const { activeCurrencyMultiplier } = this.props.commonStore;
          // alert(currentUserProfile.money);
          return (
            <div className={classNames(styles.wrapper)}>
              {/* <div className={classNames(styles.icon)}>
                <Link to="/personal-page" style={{ height: "32px" }}>
                  <img src="./assets/logo.png" alt="" />
                </Link>
              </div> */}
              <div className={classNames(styles["page-name"])}>
                <Icon name="list layout" />
                <p><a href="https://cabinet.airsolutions.club/">{this.props.commonStore.appName}</a></p>
              </div>
              <div className={classNames(styles.balance)}>
              <p>{(currentUserProfile.money * activeCurrencyMultiplier).toFixed(2)}{this.props.commonStore.activeCurrency}</p>
              </div>
              <div
                className={classNames(styles.spacer)}
                style={{
                  flexGrow: 1,
                }}
              />
              <UserCurrencyDropdown />
              <div className={classNames(styles.mail)} onClick={() => {
                window.open('mailto:support@airsolutions.club');
              }}>
                <Icon name="mail" />
              </div>
              <div className={classNames(styles.settings)}>
                <Link to="/profile">
                  <Icon name="settings" />
                </Link>
              </div>
              <UserProfileDropdown username={this.props.userStore.currentUser.username} passport={this.props.userStore.photo}/>
            </div>
          );
        }
      }
    )
  );

export { SignInned };
