import React from "react";
import styles from "../wallet-recharge/wallet-recharge.module.scss";
import { WalletHeaderMenu } from "../wallet-recharge/sub-components";
import classNames from "classnames";
import { WithdrawContent } from "./sub-components";
import { Withdraw } from "../withdraw";

interface IWalletWithdrawProps {}

const WalletWithdraw: React.FC<IWalletWithdrawProps> = props => {
  return (
    <div className={classNames(styles.wrapper)}>
      <WalletHeaderMenu />
      <WithdrawContent />
      <div className={classNames(styles.content)}>
        <div className={classNames(styles.title)}>
          <h1 style={{ paddingLeft: "30px" }}>Активные заявки на вывод средств</h1>
        </div>
        <Withdraw income={true}/>
      </div>
    </div>
  );
};

export { WalletWithdraw };
