import React, { useState } from "react";
import styles from "../tariff-card/tariff-card.module.scss";
import classNames from "classnames";
import { IPlan } from "../../types/plan.type";
import { IActivatedCardStyle } from "../deposits/deposits";
import { inject, observer } from "mobx-react";

interface IPlanCard {
  name: string;
  percent: number;
  min: number;
  max: number;
  terms: number;
}

interface IPlanCardProps {
  planCardDetails: IPlan;
  activatedCardStyle: IActivatedCardStyle;
  setCardState: Function;
}


const PlanCard = inject('commonStore')(
  observer(
    class PlanCard extends React.Component<any> {
      render() {
        const { name, percent, min, max, terms } = this.props.planCardDetails;
        const { activeCurrencyMultiplier, activeCurrency } = this.props.commonStore;
        return (
          <div
            className={classNames(styles.content, this.props.activatedCardStyle.activatedCardName === name ? styles["active-card"] : "")}
            onClick={() => this.props.setCardState(this.props.planCardDetails)}
          >
            <div className={classNames(styles["card-header"])} style={{ padding: 0 }}>
              <h1>{name}</h1>
              <h1 className={classNames(styles.percent)}>{percent}%</h1>
            </div>
            <div className={classNames(styles.range)}>
            <p>Min {(min * activeCurrencyMultiplier).toFixed(2)}{activeCurrency}-</p>
            <p>Max {(max * activeCurrencyMultiplier).toFixed(2)}{activeCurrency}</p>
            </div>
            <div className={classNames(styles.terms)}>
              <p>Срок контракта: {terms} дней</p>
            </div>
          </div>
        );
      }
    }
  )
);

// const PlanCard: React.FC<IPlanCardProps> = ({ planCardDetails, setCardState, activatedCardStyle }) => {
  
// };

export { PlanCard };
