export default function UserCurrencyDropdown() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [currency, setCurrency] = useState(commonStore.activeCurrency);

  const anchorRef = React.useRef<HTMLDivElement>(null);

  const handleToggle = () => {
    setOpen(prevOpen => !prevOpen);
  };

  const handleClose = (event: React.MouseEvent<EventTarget>) => {
    if (anchorRef.current && anchorRef.current.contains(event.target as HTMLElement)) {
      return;
    }

    setOpen(false);
  };

  const chooseCurrency = (curr: string) => {
    switch (curr) {
      case "$":
        setCurrency("$");
        commonStore.setActiveCurrency("$");
        break;
      case "₽":
        setCurrency("₽")
        commonStore.setActiveCurrency("₽");
        break;
      case "€":
        setCurrency("€")
        commonStore.setActiveCurrency("€");
        break;

      default:
        setCurrency("$")
        commonStore.setActiveCurrency("$");
        break;
    }
  };

  function handleListKeyDown(event: React.KeyboardEvent) {
    if (event.key === "Tab") {
      event.preventDefault();
      setOpen(false);
    }
  }

  // return focus to the button when we transitioned from !open -> open
  const prevOpen = React.useRef(open);
  React.useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current!.focus();
    }
    prevOpen.current = open;
  }, [open]);

  return (
    <div className={classes.root}>
      <div>
        <div
          ref={anchorRef}
          aria-controls={open ? "menu-list-grow" : undefined}
          aria-haspopup="true"
          onClick={handleToggle}
        >
          <div className={classNames(styles.currency)}>
            {currency === "$" ? <Icon name="dollar" /> : currency === "€" ? <Icon name="euro" /> : <Icon name="rouble" />}
          </div>
        </div>
        <Popper
          open={open}
          anchorEl={anchorRef.current}
          role={undefined}
          transition
          disablePortal
          className={classNames(styles.drop)}
        >
          {({ TransitionProps, placement }) => (
            <Grow
              {...TransitionProps}
              style={{
                transformOrigin: placement === "bottom" ? "center" : "center",
                boxShadow: "unset",
                borderRadius: 0,
                borderLeft: "1px solid #eaeaea",
                borderRight: "1px solid #eaeaea",
                borderBottom: "1px solid #eaeaea",
              }}
            >
              <Paper>
                <ClickAwayListener onClickAway={handleClose}>
                  <MenuList
                    autoFocusItem={open}
                    id="menu-list-grow"
                    onKeyDown={handleListKeyDown}
                    style={{ width: "54px" }}
                  >
                    <MenuItem onClickCapture={handleClose} onClick={() => chooseCurrency("$")}>
                      <Icon name="dollar" style={{margin: "0 auto", color: "#000000"}} />
                    </MenuItem>
                    <MenuItem onClickCapture={handleClose} onClick={() => chooseCurrency("€")}>
                      <Icon name="euro" style={{margin: "0 auto", color: "#000000"}} />
                    </MenuItem>
                    <MenuItem onClickCapture={handleClose} onClick={() => chooseCurrency("₽")}>
                      <Icon name="rouble" style={{margin: "0 auto", color: "#000000"}} />
                    </MenuItem>
                  </MenuList>
                </ClickAwayListener>
              </Paper>
            </Grow>
          )}
        </Popper>
      </div>
    </div>
  );
}
