import { observable, action, decorate } from 'mobx';
import agent from './agent';
import userStore from './userStore';
import commonStore from './commonStore';
import { toast } from 'react-toastify';
import { callbackify } from 'util';

class AuthStore {
  isLoading = false;
  hasError = false;
  lastError = '';
  toDashboard = false;

  values = {
    username: '',
    email: '',
    password: '',
    phone: '',
    repassword: ''
  };
  

  clearError()
  {
    this.hasError = false;
    this.lastError = '';
  }

  setError(error)
  {
    this.hasError = true;
    this.lastError = error;
  }

  setUsername(username) {
    this.values.username = username;
  }

  setEmail(email) {
    this.values.email = email;
  }

  setPassword(password) {
    this.values.password = password;
  }

  setRePassword(repassword) {
    this.values.repassword = repassword;
  }

  setPhone(phone) {
    this.values.phone = phone;
  }

  reset() {
    this.values.username = '';
    this.values.email = '';
    this.values.password = '';
    this.values.repassword = '';
    this.values.phone = '';
  }

  async login() {
    this.isLoading = true;
    this.clearError();
    const result = await agent.Auth.login(this.values.email, this.values.password);
    if(result.error)
    {
      this.setError(result.message);
    } else {
      this.toDashboard = false;
      commonStore.setToken(result.token);
      userStore.pullUser();
    }
    this.isLoading = false;
  }

  async register() {
    if(this.values.password !== this.values.repassword)
    {
      toast.error('Пароли не совадают');
      return;
    }
    this.isLoading = true;
    this.clearError();
    const result = await agent.Auth.register(this.values.username, this.values.email, this.values.password, this.values.phone);
    if(result.error)
    {
      this.setError(result.message);
      toast.error(result.message, {
        position: toast.POSITION.BOTTOM_LEFT
      });
    } else {
      await this.login(this.values.username, this.values.password);
      // userStore.pullUser();
    }
    this.isLoading = false;
  }

  logout() {
    commonStore.setToken(undefined);
    userStore.forgetUser();
    return Promise.resolve();
  }

  disableToDashboard() {
    this.toDashboard = false;
  }
}

decorate(AuthStore, {
  isLoading: observable,
  hasError: observable,
  lastError: observable,
  values: observable,
  clearError: action,
  setError: action,
  setUsername: action,
  setEmail: action,
  setPassword: action,
  reset: action,
  login: action,
  register: action,
  logout: action,
  toDashboard: observable,
  disableToDashboard: action
});

export default new AuthStore();
