import React from "react";
import styles from "../../header.module.scss";
import Button from "@material-ui/core/Button";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Grow from "@material-ui/core/Grow";
import Paper from "@material-ui/core/Paper";
import Popper from "@material-ui/core/Popper";
import MenuItem from "@material-ui/core/MenuItem";
import MenuList from "@material-ui/core/MenuList";
import { makeStyles, createStyles, Theme } from "@material-ui/core/styles";
import classNames from "classnames";
import { Image, Icon } from "semantic-ui-react";
import { NavLink } from "react-router-dom";
import authStore from './../../../../stores/authStore';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
    },
    paper: {
      marginRight: theme.spacing(2),
    },
  }),
);

export default function UserProfileDropdown(props:any) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const anchorRef = React.useRef<HTMLDivElement>(null);

  const handleToggle = () => {
    setOpen(prevOpen => !prevOpen);
  };

  const handleClose = (event: React.MouseEvent<EventTarget>) => {
    if (anchorRef.current && anchorRef.current.contains(event.target as HTMLElement)) {
      return;
    }

    setOpen(false);
  };

  function handleListKeyDown(event: React.KeyboardEvent) {
    if (event.key === "Tab") {
      event.preventDefault();
      setOpen(false);
    }
  }

  // return focus to the button when we transitioned from !open -> open
  const prevOpen = React.useRef(open);
  React.useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current!.focus();
    }
    prevOpen.current = open;
  }, [open]);

  return (
    <div className={classes.root}>
      <div>
        <div
          ref={anchorRef}
          aria-controls={open ? "menu-list-grow" : undefined}
          aria-haspopup="true"
          onClick={handleToggle}
        >
          <div className={classNames(styles["menu-wrapper"])}>
            <div className={classNames(styles.menu)}>
              <div className={classNames(styles["user-info"])}>
                <Image src={props.passport ? props.passport : './assets/avatar.jpg'} avatar size="mini" />
                <div className={classNames(styles["user-info-text"])}>
                  <span>{props.username}</span>
                  {/* <p>Status</p> */}
                </div>
              </div>
              <div className={classNames(styles["menu-dropdown"])}>
                <Icon name="sort amount down" />
              </div>
            </div>
          </div>
        </div>
        <Popper
          open={open}
          anchorEl={anchorRef.current}
          role={undefined}
          transition
          disablePortal
          className={classNames(styles.drop)}
        >
          {({ TransitionProps, placement }) => (
            <Grow
              {...TransitionProps}
              style={{
                transformOrigin: placement === "bottom" ? "center" : "center",
                boxShadow: "unset",
                borderRadius: 0,
                borderLeft: "1px solid #eaeaea",
                borderRight: "1px solid #eaeaea",
                borderBottom: "1px solid #eaeaea",
              }}
            >
              <Paper>
                <ClickAwayListener onClickAway={handleClose}>
                  <MenuList
                    autoFocusItem={open}
                    id="menu-list-grow"
                    onKeyDown={handleListKeyDown}
                    style={{ width: "270px" }}
                  >
                    <MenuItem onClick={handleClose}>
                      <NavLink
                        to="/profile"
                        style={{ fontSize: "13px", color: "black", fontWeight: 100, padding: "8px" }}
                      >
                        Профиль
                      </NavLink>
                    </MenuItem>
                    <MenuItem onClick={handleClose} className={classNames(styles["mobile-mail"])}>
                      <NavLink to="#" style={{ fontSize: "13px", color: "black", fontWeight: 100, padding: "8px" }}>
                        Написать нам
                      </NavLink>
                    </MenuItem>
                    <MenuItem
                      onClick={e => {
                        handleClose(e);
                        authStore.logout();
                      }}
                    >
                      <NavLink
                        to="/sign-in"
                        style={{ fontSize: "13px", color: "black", fontWeight: 100, padding: "8px" }}
                      >
                        Выход
                      </NavLink>
                    </MenuItem>
                  </MenuList>
                </ClickAwayListener>
              </Paper>
            </Grow>
          )}
        </Popper>
      </div>
    </div>
  );
}
