import React from "react";
import styles from "./wallet-recharge.module.scss";
import classNames from "classnames";
import { WalletHeaderMenu, WalletContent } from "./sub-components";
import { Withdraw } from "../withdraw";

interface IWalletRechargeProps {}

const WalletRecharge: React.FC<IWalletRechargeProps> = props => {
  return (
    <div className={classNames(styles.wrapper)}>
      <WalletHeaderMenu />
      <WalletContent />
      <div className={classNames(styles.content)}>
        <div className={classNames(styles.title)}>
          <h1 className={classNames(styles["widthraw-request"])}>Активные заявки на пополнение баланса</h1>
        </div>
        <Withdraw outcome={true}/>
      </div>
    </div>
  );
};

export { WalletRecharge };
