import React from "react";
import styles from "./tariff-card.module.scss";
import classNames from "classnames";
import { PlanCard } from "../plan-card";
import { IPlan } from "../../types/plan.type";
import { IActivatedCardStyle } from '../deposits/deposits';

interface ITariffCardProps {
  plan: IPlan[];
  setCardState: Function;
  activatedCardStyle: IActivatedCardStyle;
}

const TariffCard: React.FC<ITariffCardProps> = ({ plan, setCardState, activatedCardStyle }) => {

  return (
    <div className={classNames(styles.container)}>
      {plan.map((card, index) => {
        return <PlanCard key={index} planCardDetails={card} setCardState={setCardState} activatedCardStyle={activatedCardStyle}  />;
      })}
    </div>
  );
};

export { TariffCard };
