import React, { useState, useEffect } from "react";
import styles from "./deposit.module.scss";
import stylesWidthdraw from "../wallet-recharge/wallet-recharge.module.scss";
import classNames from "classnames";
import { NavLink } from "react-router-dom";
import { Profits } from "./sub-components/profits";
import { SummSettings } from "./sub-components/summ-settings";
import { TariffCard } from "../tariff-card";
import { IPlan } from "../../types/plan.type";
import { SliderSum } from "../slider-sum";
import { initialTariffRobot, initialTariffDeposit } from "../../plans/initial-tariffs";
import { DEPOSIT_PLANS } from "../../plans";
import { ROBOT_PLANS } from "../../plans/robot.plans";
import { Withdraw } from "../withdraw";
import { DepositList } from "../depositList";
import { TradeList } from "../tradeList";

interface IDepositsProps {
  namePage: Page;
}

type Page = "deposits" | "robots";

export interface IActivatedCardStyle {
  activatedCardName: string;
}

const setPage = (name: Page) => (name === "deposits" ? "deposits" : "robots");

const Deposits: React.FC<IDepositsProps> = ({ namePage }) => {
  const [selectedPage, setSelectedPage] = useState<Page>(setPage(namePage));

  const setInitialTariff = () => {
    return selectedPage === "deposits" ? initialTariffDeposit : initialTariffRobot;
  };

  const [activatedCard, setActivatedCard] = useState<IPlan>(setInitialTariff());
  const [investmentSumm, setInvestmentSumm] = useState(0);
  const [activatedCardStyle, setActivatedCardStyle] = useState<IActivatedCardStyle>({ activatedCardName: "Начальный" });

  useEffect(() => {
    setActivatedCard(setInitialTariff());
  }, [selectedPage]);


  useEffect(() => {
    setActivatedCardStyle({ activatedCardName: activatedCard.name });
  }, [activatedCard]);

  return (
    <div className={classNames(styles.wrapper)}>
      <div className={classNames(styles["deposit-header"])}>
        <NavLink
          to="/deposits"
          onClick={() => setSelectedPage("deposits")}
          activeClassName={classNames(styles["active-item"])}
          className={classNames(styles.link)}
        >
          Депозиты
        </NavLink>
        <NavLink
          to="/robots"
          onClick={() => setSelectedPage("robots")}
          activeClassName={classNames(styles["active-item"])}
          className={classNames(styles.link)}
        >
          Торговые Роботы
        </NavLink>
      </div>
      <div className={classNames(styles.content)}>
        <div className={classNames(styles["deposit-settings"])}>
          <div className={classNames(styles.title)}>
            <h1>Выберите инвестиционный план</h1>
          </div>
          <SummSettings investmentSumm={investmentSumm} />
          <SliderSum
            max={activatedCard.max}
            min={activatedCard.min}
            step={activatedCard ? activatedCard.sliderStepRange : setInitialTariff().sliderStepRange}
            sumToShow={setInvestmentSumm}
          />
          <div className={classNames(styles["deposit-info"])}>
            <div>
              <h3>Описание выбранного инвестиционного плана</h3>
            </div>
            <Profits currentCardData={activatedCard} investmentSumm={investmentSumm} page={selectedPage}/>
          </div>
        </div>
        <div className={classNames(styles["deposit-variation"])}>
          <TariffCard
            plan={selectedPage === "deposits" ? DEPOSIT_PLANS : ROBOT_PLANS}
            activatedCardStyle={activatedCardStyle}
            setCardState={setActivatedCard}
          />
        </div>
      </div>
      <div className={classNames(stylesWidthdraw.content)} style={{marginTop: "30px"}}>
        <div className={classNames(stylesWidthdraw.title)}>
          <h1 style={{paddingLeft: "30px"}}>Активные депозиты</h1>
        </div>
        {selectedPage === 'deposits' ? <DepositList /> : (<TradeList/>)}
        
      </div>
    </div>
  );
};

export { Deposits };
