import React, { useState } from "react";
import styles from "../../../sign-up/sign-up.module.scss";
import classNames from "classnames";
import { Input, Icon } from "semantic-ui-react";
import { toggleHidePassword } from "../../../../helpers/auth.helper";

interface IPasswordProps {
  title: string;
  value: string;
  onChange: any;
}

const Password: React.FC<IPasswordProps> = ({title, value, onChange}) => {
  const [isHidePassword, setIsHidePassword] = useState(true);
  return (
    <>
      <div className={classNames(styles["input"], styles["input-with-label"])}>
        <Input
          icon={
            <Icon
              name={isHidePassword ? "eye" : "eye slash"}
              size="large"
              onClick={() => toggleHidePassword(!isHidePassword, setIsHidePassword)}
              link
            />
          }
          value={value}
          onChange={onChange}
          type={isHidePassword ? "password" : "text"}
          placeholder={title}
        />
        <p>Минимальная длина пароля - 8 символов</p>
      </div>
    </>
  );
};

export { Password };
