import React from "react";
import styles from "../../wallet-recharge.module.scss";
import { NavLink } from 'react-router-dom';
import classNames from 'classnames';

interface IWalletHeaderMenuProps {}

const WalletHeaderMenu: React.FC<IWalletHeaderMenuProps> = props => {
  return (
    <div className={classNames(styles["wallet-header"])}>
      <NavLink
        to="/wallet"
        activeClassName={classNames(styles["active-item"])}
        className={classNames(styles.link)}
      >
        Ввод средств
      </NavLink>
      <NavLink
        to="/withdraw"
        activeClassName={classNames(styles["active-item"])}
        className={classNames(styles.link)}
      >
        Вывод средств
      </NavLink>
    </div>
  );
};

export { WalletHeaderMenu };
