import React, { useState } from "react";
import styles from "../../../sign-up/sign-up.module.scss";
import classNames from "classnames";
import { toggleHidePassword } from "../../../../helpers/auth.helper";
import { Input, Icon } from "semantic-ui-react";

interface IRePasswordProps {
  title: string;
  value: string;
  onChange: any;
}

const RePassword: React.FC<IRePasswordProps> = ({title, value, onChange}) => {
  const [isHideRePassword, setIsHideRePassword] = useState(true);
  return (
    <>
      <div className={classNames(styles["input"], styles["input-with-label"])}>
        <Input
          icon={
            <Icon
              name={isHideRePassword ? "eye" : "eye slash"}
              size="large"
              onClick={() => toggleHidePassword(!isHideRePassword, setIsHideRePassword)}
              link
            />
          }
          value={value}
          onChange={onChange}
          type={isHideRePassword ? "password" : "text"}
          placeholder={title}
        />
        <p>Минимальная длина пароля - 8 символов</p>
      </div>
    </>
  );
};

export { RePassword };
