import React from "react";
import styles from "../../../sign-up/sign-up.module.scss";
import stylesSidebar from "./user-info.module.scss";
import classNames from "classnames";
import { Icon, Image } from "semantic-ui-react";


import { inject, observer } from "mobx-react";

const ImageUpload = inject('userStore')(
  observer(
    class ImageUpload extends React.Component<any, any> {
      constructor(props:any) {
        super(props);
        this.state = {file: '',imagePreviewUrl: this.props.userStore.photo};
      }

      _handleSubmit(e:any) {
        e.preventDefault();
        console.log('handle uploading-');
        console.log(this.state.imagePreviewUrl);
      }

      _handleImageChange(e:any) {
        e.preventDefault();

        let reader = new FileReader();
        let file = e.target.files[0];

        reader.onloadend = () => {
          this.setState({
            file: file,
            imagePreviewUrl: reader.result
          });
          this.props.userStore.setPassport(reader.result);
        }

        // console.log(reader.result);

        reader.readAsDataURL(file)
      }

      

      render() {

        return (
          <div className="fileContainer">
              <input
              accept="image/x-png"
              id='fileInput'
              className={classNames(stylesSidebar.fileInput)}
            type="file" 
            onChange={(e)=>this._handleImageChange(e)} />
            <Icon name="cloud download" style={{ color: "rgb(47, 128, 237)" }} onClick={() => document.getElementById('fileInput')?.click()}/>
          </div>
        )
      }
    }
  )
)

const UserInfo = inject('userStore')(
  observer(
    class UserInfo extends React.Component<any> {
      render() {
        return (
          <div className={classNames(stylesSidebar.sidebar)}>
            <div>
              <div className={classNames(stylesSidebar.image)}>
                <span>
                  <Image src={this.props.userStore.photo ? this.props.userStore.photo : './assets/avatar.jpg'} size="small" avatar />
                  <div>
                    <ImageUpload/>
                    
                  </div>
                </span>
              </div>
              <div className={classNames(styles["user-text"])}>
                <p
                  style={{
                    fontSize: "16px",
                    borderBottom: "1px solid rgb(229, 230, 235)",
                    fontWeight: "bold",
                    paddingBottom: "40px",
                  }}
                >
                  {this.props.userStore.currentUser.username}
                </p>
                <p style={{ fontSize: "10px", margin: 0, color: "rgb(25, 35, 60)", fontWeight: "lighter" }}>
                  Никнейм профиля:
                </p>
                <p style={{ fontSize: "10px", margin: 0, color: "rgb(25, 35, 60)", fontWeight: "lighter" }}>{this.props.userStore.currentUser.username}</p>
              </div>
            </div>
          </div>
        );
      }
    }
  )
)
export { UserInfo };
