import React from "react";
import styles from "../../../deposits/deposit.module.scss";
import classNames from "classnames";
import { Button } from "semantic-ui-react";
import { IPlan } from "../../../../types/plan.type";
import packageStore from '../../../../stores/packageStore';
import commonStore from '../../../../stores/commonStore';

interface IInvestProps {
  currentCardData: IPlan;
  investmentSumm: number;
  page: string;
}

const Invest: React.FC<IInvestProps> = ( { currentCardData, investmentSumm, page } ) => {
  return (
    <div className={classNames(styles.invest)}>
      {page === 'robots' ? (
        <p>Прибыль по выбранному торговому роботу начисляется разово в конце срока инвестирования</p>
      ) : (
        <p>Прибыль по выбранному депозиту начисляется с частотой: один раз в 1 день.</p>
      )}
      
      <p>Фондовый сбор равен 1%. Автоматически списывается за управление активами.</p>
      <div className={classNames(styles.btn)}>
        <Button
        onClick={() => {
          packageStore.createPackage(currentCardData, investmentSumm * commonStore.activeCurrencyMultiplierReverse, page);
        }}
        >
          Инвестировать
          </Button>
      </div>
    </div>
  );
};

export { Invest };
