import { observable, action, reaction, decorate, computed, toJS } from 'mobx';
import userStore from './userStore';
import agent from './agent';
import { toast } from 'react-toastify';
import commonStore from './commonStore';
import emailjs from 'emailjs-com';

class PackageStore {

  isLoading = false;
  hasError = false;
  lastError = '';
  depositsRegistry = observable.map();
  tradesRegistry = observable.map();
  myTransactionsRegistry = observable.map();
  transactionsRegistry = observable.map();
  activeUserIdInAdmin = 1;

  setActiveUserId(id) {
    this.activeUserIdInAdmin = id;
  }

  get deposits()
  {
    const array = Array.from(this.depositsRegistry.values()).reverse();
    const jsArray = array.map(item => {
      return toJS(item);
    })
    return jsArray;
  }

  get trades()
  {
    const array = Array.from(this.tradesRegistry.values()).reverse();
    const jsArray = array.map(item => {
      return toJS(item);
    })
    return jsArray;
  }

  get myTransactions()
  {
    const array = Array.from(this.myTransactionsRegistry.values()).reverse();
    const jsArray = array.map(item => {
      return toJS(item);
    })
    return jsArray;
  }

  get transactionsByActiveUserId()
  {
    const array = Array.from(this.transactionsRegistry.values()).reverse();
    const jsArray = array.map(item => {
      return toJS(item);
    })
    const filtered =  jsArray.filter(item => {
      // console.log(item.sender + ' ' + item.recipient + ' ' + id + ' ')
      return ((+item.sender === +this.activeUserIdInAdmin) || (+item.recipient === +this.activeUserIdInAdmin)); 
    });
    // console.log(filtered);
    return filtered;
  }

  get myFinishedOutcomeTransactionsSumm()
  {
    const array = Array.from(this.myTransactionsRegistry.values()).reverse();
    const jsArray = array.map(item => {
      return toJS(item);
    })
    const finalSumm = jsArray.reduce((summ, item) => {
      // console.log()
      if(item.status === 'Обработано' && +item.sender === +userStore.currentUser.id && item.paymentSystem !== 'AirSolutions')
      {
        summ += item.amount;
      }
      return summ;
    }, 0);
    // console.log(finalSumm);
    return finalSumm;
  }

  get myFinishedIncomeTransactionsSumm()
  {
    const array = Array.from(this.myTransactionsRegistry.values()).reverse();
    const jsArray = array.map(item => {
      return toJS(item);
    })
    const finalSumm = jsArray.reduce((summ, item) => {
      // console.log()
      if(item.status === 'Обработано' && +item.recipient === +userStore.currentUser.id && item.paymentSystem !== 'AirSolutions')
      {
        summ += item.amount;
      }
      return summ;
    }, 0);
    // console.log(finalSumm);
    return finalSumm;
  }

  get myNotFinishedOutcomeTransactionsSumm()
  {
    const array = Array.from(this.myTransactionsRegistry.values()).reverse();
    const jsArray = array.map(item => {
      return toJS(item);
    })
    const finalSumm = jsArray.reduce((summ, item) => {
      // console.log()
      if(item.status === 'В ожидании' && +item.sender === +userStore.currentUser.id && item.paymentSystem !== 'AirSolutions')
      {
        summ += item.amount;
      }
      return summ;
    }, 0);
    // console.log(finalSumm);
    return finalSumm;
  }

  get myNotFinishedIncomeTransactionsSumm()
  {
    const array = Array.from(this.myTransactionsRegistry.values()).reverse();
    const jsArray = array.map(item => {
      return toJS(item);
    })
    const finalSumm = jsArray.reduce((summ, item) => {
      console.log()
      if(item.status === 'В ожидании' && +item.recipient === +userStore.currentUser.id && item.paymentSystem !== 'AirSolutions')
      {
        summ += item.amount;
      }
      return summ;
    }, 0);
    // console.log(finalSumm);
    return finalSumm;
  }

  clearError()
  {
    this.hasError = false;
    this.lastError = '';
  }

  setError(error)
  {
    this.hasError = true;
    this.lastError = error;
  }

  async loadPackages()
  {
      this.isLoading = true;
      this.depositsRegistry.clear();
      this.tradesRegistry.clear();
      const result = await agent.Packages.list();
      result.forEach(async packaGe => {
        let packagE = toJS(packaGe);
        const oneDay = 24 * 60 * 60 * 1000;
        const needFinish = new Date(packagE.endDate) < new Date(new Date().getTime() + (0 * oneDay));
        if(needFinish && packagE.status === 1){
          const firstDate = new Date(packagE.endDate);
          const secondDate = new Date(packagE.startDate);
  
          const diffDays = Math.round(Math.abs((firstDate - secondDate) / oneDay));
          const finishData = {
            sender: commonStore.appName,
            user: userStore.currentUser.id,
            capital: packagE.capital,
            dailyProfit: packagE.dailyProfit,
            term: diffDays,
          }
          await agent.Packages.finish(finishData);
          packagE.status = 2;
          await agent.Packages.update(packagE);
        }
  
        if(packagE.user === userStore.currentUser.id)
        {
          if(packagE.control === 1)
          {
            this.depositsRegistry.set(packagE.id, packagE);
          }
          if(packagE.control === 2)
          {
            this.tradesRegistry.set(packagE.id, packagE);
          }
        }
      });
      await userStore.pullUser();
      this.isLoading = false;
  }

  async createPackage(card, summ, type)
  {
    this.isLoading = true;
    const packageData = {
      name: card.name,
      type: card.type,
      control: type === 'deposits' ? 1 : 2,
      user: userStore.currentUser.id,
      cost: summ,
      dailyProfit: type === 'deposits' ? card.percent : card.percent / 30,
      robotTerm: card.terms
    };
    const result = await agent.Packages.create(packageData);
    if(result.error)
    {
      this.setError(result.message);
      toast.error(result.message);
    } else {
      const item = toJS(result);
      if(type === 'deposits')
      {
        this.depositsRegistry.set(item.id, item);
      } else {
        this.tradesRegistry.set(item.id, item);
      }
      // await this.loadPackages();
      toast.success('Вы успешно совершили депозит!');
      await userStore.pullUser();
    }
    this.isLoading = false;
  }

  async loadMyTransactions()
  {
    this.isLoading = true;
    this.transactionsRegistry.clear();
    this.myTransactionsRegistry.clear();
    const result = await agent.Transactions.list();
    result.forEach(transactionProxy => {
      const transaction = toJS(transactionProxy);
      if(+transaction.recipient === +userStore.currentUser.id || +transaction.sender === +userStore.currentUser.id)
      {
        // console.log(transaction);
        this.myTransactionsRegistry.set(transaction.id, transaction);
      }
      this.transactionsRegistry.set(transaction.id, transaction)
    });
    this.isLoading = false;
  }

  async createTransaction(transaction)
  {
    this.isLoading = true;
    const resultProxy = await agent.Transactions.create(transaction);
    const result = toJS(resultProxy);
    this.myTransactionsRegistry.set(result.id, result);
    toast.success('Транзакция успешно создана!')
    this.isLoading = false;
  }

  async submitTransaction(id)
  {
    this.isLoading = true;
    const transaction = toJS(this.transactionsRegistry.get(id));
    console.log(transaction);
    const userId = Number(transaction.sender) > 0 ? transaction.sender : Number(transaction.recipient);
    const profile = await agent.Auth.getProfileByUserId(userId);
    const email = toJS(userStore.usersandprofilesRegistry.get(profile.id)).user.email;
    // await emailjs.send("smtp_server", "change_balance", {host: window.location.hostname, title: 'status', before: transaction.status, now: 'Обработано', email, password: profile.password}, 'user_h1pqEqKiVUD1zsuXPjKGk');
    transaction.status = 'Обработано';
    const resultProxy = await agent.Transactions.update(transaction);
    const result = toJS(resultProxy);
    console.log(result);
    this.transactionsRegistry.set(result.id, result);
    console.log(toJS(this.transactionsRegistry.get(result.id)));
    const activeUserId = this.activeUserIdInAdmin;
    this.activeUserIdInAdmin = 0;
    this.activeUserIdInAdmin = activeUserId;
    this.isLoading = false;
    // window.location.reload();
  }

  async cancelTransaction(id)
  {
    this.isLoading = true;
    const transaction = toJS(this.transactionsRegistry.get(id));
    const userId = Number(transaction.sender) > 0 ? transaction.sender : Number(transaction.recipient);
    const profile = await agent.Auth.getProfileByUserId(userId);
    const email = toJS(userStore.usersandprofilesRegistry.get(profile.id)).user.email;
    // await emailjs.send("smtp_server", "change_balance", {host: window.location.hostname, title: 'status', before: transaction.status, now: 'Отклонено', email, password: profile.password}, 'user_h1pqEqKiVUD1zsuXPjKGk');
    transaction.status = 'Отклонено';
    console.log(transaction);
    const resultProxy = await agent.Transactions.update(transaction);
    const result = toJS(resultProxy);
    console.log(result);
    this.transactionsRegistry.set(result.id, result);
    this.isLoading = false;
    // window.location.reload();
  }

  async waitTransaction(id)
  {
    this.isLoading = true;
    const transaction = toJS(this.transactionsRegistry.get(id));
    // await emailjs.send("smtp_server", "change_balance", {title: 'status', before: transaction.status, now: 'В ожидании', email: userStore.currentUserProfile.password}, 'user_h1pqEqKiVUD1zsuXPjKGk');
    
    const userId = Number(transaction.sender) > 0 ? transaction.sender : Number(transaction.recipient);
    const profile = await agent.Auth.getProfileByUserId(userId);
    const email = toJS(userStore.usersandprofilesRegistry.get(profile.id)).user.email;
    // await emailjs.send("smtp_server", "change_balance", {host: window.location.hostname, title: 'status', before: transaction.status, now: 'В ожиданиионено', email, password: profile.password}, 'user_h1pqEqKiVUD1zsuXPjKGk');
    transaction.status = 'В ожидании';
    console.log(transaction);
    const resultProxy = await agent.Transactions.update(transaction);
    const result = toJS(resultProxy);
    console.log(result);
    this.transactionsRegistry.set(result.id, result);
    this.isLoading = false;
    // window.location.reload();
  }

}



decorate(PackageStore, {
  isLoading: observable,
  hasError: observable,
  activeUserIdInAdmin: observable,
  lastError: observable,
  depositsRegistry: observable,
  tradesRegistry: observable,
  trades: computed,
  deposits: computed,
  myFinishedOutcomeTransactionsSumm: computed,
  myFinishedIncomeTransactionsSumm: computed,
  myNotFinishedOutcomeTransactionsSumm: computed,
  myNotFinishedIncomeTransactionsSumm: computed,
  clearError: action,
  setError: action,
  setToken: action,
  loadPackages: action,
  createPackage: action,
  loadMyTransactions: action,
  createTransaction: action,
  submitTransaction: action,
  cancelTransaction: action,
  setActiveUserId: action,
  waitTransaction: action,
  transactionsByActiveUserId: computed,
});

export default new PackageStore();
