import React from "react";
import styles from "./user-profile.module.scss";
import classNames from "classnames";
import { UserInfo, PersonalData, UserProfileHeader, SecurityData, DocumentVerification } from "./sub-components";
import { useState } from "react";
import { useEffect } from 'react';

interface IUserProfileProps {}

const setPage = (name: string) => {
  switch (name) {
    case "Профиль":
      return <PersonalData />;
    case "Безопасность":
      return <SecurityData />;
    // case "Верификация документов":
    //   return <DocumentVerification />;

    default:
      break;
  }
};

const UserProfile: React.FC<IUserProfileProps> = props => {
  const [currentTab, setCurrentTab] = useState("Профиль");
  
  return (
    <div className={classNames(styles.wrapper)}>
      <UserProfileHeader setCurrentTab={setCurrentTab} />
      <div className={classNames(styles.content)}>
        <UserInfo />
        {setPage(currentTab)}
      </div>
    </div>
  );
};

export { UserProfile };
