import React from "react";
import styles from "../../../../../components/sign-up/sign-up.module.scss";
import classNames from "classnames";
import { Link } from "react-router-dom";
import { Checkbox } from "semantic-ui-react";

interface IAcceptRulesProps {
  checked: boolean;
  onChange: any;
}

const AcceptRules: React.FC<IAcceptRulesProps> = props => {
  return (
    <div className={classNames(styles["accept-rules"])}>
      <div className={classNames(styles.checkbox)}>
        <Checkbox checked={props.checked} onChange={props.onChange}/>
      </div>
      <div className={classNames(styles.rules)}>
        <p>
          Я принимаю условия <a href="https://airsolutions.club/rules.php" target="blank">Пользовательского соглашения </a> и даю своё согласие на обработку моей
          персональной информации на условиях, определенных <a href="http://airsolutions.club/confidential.php" target="blank">Политикой конфиденциальности.</a>
        </p>
      </div>
    </div>
  );
};

export { AcceptRules };
