import React from "react";
import styles from "../../../wallet-recharge/wallet-recharge.module.scss";
import classNames from "classnames";
import { Input, Icon } from "semantic-ui-react";
import { Cards } from "../../../wallet-recharge/sub-components";
import { WITHDRAW_VARIATIONS } from "../../../../plans/payment-variants";
import { inject, observer } from "mobx-react";

interface IWithdrawContentProps {}

const WithdrawContent = inject('commonStore', 'authStore')(
  observer(
    class WithdrawContent extends React.Component<any> {

      state = {
        amount: 0
      }

      handleAmountChage = (e:any) => this.setState({amount: e.target.value});

      render() {
        return (
          <div className={classNames(styles.content)} style={this.props.commonStore.sidebarVisible ? {paddingRight: 100} : {}}>
            <div>
              <div className={classNames(styles.title)}>
                <h1>Вывод средств</h1>
              </div>
              <div className={classNames(styles["payment-settings"])}>
                <div>
                  <h3>Укажите сумму вывода и валюту:</h3>
                </div>
                <div className={classNames(styles["input-summ"])}>
                  <Input focus={true} placeholder="Сумма" value={this.state.amount === 0 ? null : this.state.amount} onChange={this.handleAmountChage}/>
                </div>
                <div className={classNames(styles.currency)}>
                  <Icon name={this.props.commonStore.activeCurrencyIcon} className={classNames(styles.icon)} />
                </div>
              </div>
            </div>
            <div className={classNames(styles.cards)}>
              <h4 style={{marginBottom: "15px"}}>Выберите платежную систему:</h4>
            </div>
            <Cards paymentVariants={WITHDRAW_VARIATIONS} amount={this.state.amount}/>
          </div>
        );
      }
    }
  )
);

export { WithdrawContent };
