import React from "react";
import styles from "./user-profile-header.module.scss";
import classNames from 'classnames';
import { NavLink } from "react-router-dom";

interface IUserProfileHeaderProps {
  setCurrentTab: Function;
}

const UserProfileHeader: React.FC<IUserProfileHeaderProps> = ({setCurrentTab}) => {
  return (
    <div>
      <div className={classNames(styles["user-profile-header"])}>
        <NavLink
          to="/profile"
          onClick={() => setCurrentTab("Профиль")}
          activeClassName={classNames(styles["active-item"])}
          className={classNames(styles.link)}
        >
          Профиль
        </NavLink>
        <NavLink
          to="/security"
          onClick={() => setCurrentTab("Безопасность")}
          activeClassName={classNames(styles["active-item"])}
          className={classNames(styles.link)}
        >
          Безопасность
        </NavLink>
        {/* <NavLink
          to="/verification"
          onClick={() => setCurrentTab("Верификация документов")}
          activeClassName={classNames(styles["active-item"])}
          className={classNames(styles.link)}
        >
          Верификация документов
        </NavLink> */}
      </div>
    </div>
  );
};

export { UserProfileHeader };
