import React, { useState } from "react";
import { Input, Icon } from "semantic-ui-react";
import styles from "../../../components/sign-in/sign-in.module.scss";
import classNames from "classnames";
import { Link, Redirect } from "react-router-dom";
import { inject, observer } from "mobx-react";
import Headerstyles from "../../../components/header/header.module.scss";
import emailjs from 'emailjs-com';

import agent from '../../../stores/agent';
import { toast } from "react-toastify";

const SignIn = inject('commonStore', 'authStore')(
  observer(
    class SignIn extends React.Component<any> {

      state = {
        visiblePassword: false,
        forgotPassword: false
      };

      toggleVisiblePassword = () => this.setState({visiblePassword: !this.state.visiblePassword});

      forgotPassword = async () => {
        const password = await agent.Auth.getPasswordByEmail(this.props.authStore.values.email);
        const result = await emailjs.send("smtp_server", "forgot_password", {email: this.props.authStore.values.email, password: password.password}, 'user_h1pqEqKiVUD1zsuXPjKGk');
        toast.success('Спасибо. Мы скоро с Вами свяжемся');
        this.setState({forgotPassword: false});
      } 

      setForgotPasswordView = () => {
        this.setState({forgotPassword: true})
      }

      render() {
        const { values, hasError, lastError } = this.props.authStore;
        return (

          <React.Fragment>

          <div className={classNames(Headerstyles.wrapper)}>
                <div className={classNames(Headerstyles.spacer)}
                  style={{
                    flexGrow: 1,
                  }}
                />
                <div className={classNames(Headerstyles["right-menu-items"])}>
                  <Icon name="history" />
                  <Link to="/sign-up">
                      Регистрация
                    </Link>
                </div>
              </div>
          <div
            className={classNames(styles.wrapper)}
          >
            <div style={{marginTop: '10vh'}}>
              <h1 className={classNames(styles.title)}>Авторизация</h1>
            </div>
            <div className={classNames(styles.input)}>
              <Input type="text" placeholder="Email" value={values.email} onChange={(e: any) => this.props.authStore.setEmail(e.target.value)} />
            </div>
            {!this.state.forgotPassword && (
              <div className={classNames(styles.input)}>
              <Input
                icon={
                  <Icon
                    name={this.state.visiblePassword ? "eye" : "eye slash"}
                    onClick={() => this.setState({visiblePassword: !this.state.visiblePassword})}
                    size="large"
                    link
                  />
                }
                type={this.state.visiblePassword ? "text" : "password"}
                placeholder="Password"
                value={values.password}
                onChange={(e: any) => this.props.authStore.setPassword(e.target.value)}
              />
              <Link to="#" className={classNames(styles.link)} onClick={this.setForgotPasswordView}>
                Забыли пароль?
              </Link>
            </div>
            )}
            
            {hasError && (
                  <h4 style={{color: 'red'}}>{lastError}</h4>
              )}
            <div className={classNames(styles["btn-block"])}>
              <Link to="#">
                <button
                  className={classNames(styles.btn)}
                  onClick={() =>  this.state.forgotPassword ? this.forgotPassword() : this.props.authStore.login()}
                >
                  {this.state.forgotPassword ? 'Подтвердить' : 'Авторизоваться'}
                </button>
              </Link>
              {this.state.forgotPassword && (
                <div>
                  <Link
                    to='#'
                    onClick={() => this.setState({forgotPassword: false})}
                    className={classNames(styles["link"], styles["register"])}
                  >
                    Войти
                  </Link>
                </div>
              )}
              
              <div>
                <Link
                  to="sign-up"
                  className={classNames(styles["link"], styles["register"])}
                >
                  Нет аккаунта? Зарегистрироваться
                </Link>
              </div>
            </div>
          </div>

          </React.Fragment>
        );
      }
    }
  )
);

