import React from "react";
import classNames from "classnames";
import styles from "./personal-page.module.scss";
import { Icon, List, Button } from "semantic-ui-react";
import { Withdraw } from "../withdraw";
import { deref, useAtom } from "@dbeining/react-atom";
import { authAtom } from "../../store";
import { AdminDashboard } from "./admin-dashboard";
import { inject, observer } from "mobx-react";
import { Link } from 'react-router-dom';

interface IPersonalPageProps {}

const PersonalPage = inject('packageStore', 'userStore', 'commonStore')(
  observer(
    class PersonalPage extends React.Component<any> {

      componentWillMount() {
        // this.props.packagesStore.loadTransactions();
      }

      render() {
        const { myFinishedOutcomeTransactionsSumm, myFinishedIncomeTransactionsSumm, myNotFinishedOutcomeTransactionsSumm, myNotFinishedIncomeTransactionsSumm } = this.props.packageStore;
        const { currentUserProfile } = this.props.userStore;
        const { activeCurrencyMultiplier, activeCurrency } = this.props.commonStore;
        return (
          <div className={classNames(styles.wrapper)}>
            {+this.props.userStore.currentUser.id !== 1 && (
              <div className={classNames(styles.sidebar)}>
              <div className={classNames(styles.balance)}>
                <span>Баланс</span>
              </div>
        <div className={classNames(styles["current-balance"])}>{(currentUserProfile.money * activeCurrencyMultiplier).toFixed(2)}{activeCurrency}</div>
              <div className={classNames(styles["balance-info"])}>
                <List>
                  <List.Item className={classNames(styles.item)}>
                    <p>
                        Инвестировано: <span>{activeCurrency} {(myFinishedOutcomeTransactionsSumm * activeCurrencyMultiplier).toFixed(2)}</span>
                    </p>
                  </List.Item>
                  <List.Item className={classNames(styles.item)}>
                    <p>
                      Выведено: <span>{activeCurrency} {(myFinishedIncomeTransactionsSumm * activeCurrencyMultiplier).toFixed(2)}</span>
                    </p>
                  </List.Item>
                  <List.Item className={classNames(styles.item)}>
                    <p>
                      Ожидает внесения: <span>{activeCurrency} {(myNotFinishedOutcomeTransactionsSumm * activeCurrencyMultiplier).toFixed(2)}</span>
                    </p>
                  </List.Item>
                  <List.Item className={classNames(styles.item)}>
                    <p>
                      Ожидает вывода: <span>{activeCurrency} {(myNotFinishedIncomeTransactionsSumm * activeCurrencyMultiplier).toFixed(2)}</span>
                    </p>
                  </List.Item>
                </List>
              </div>
              <div className={classNames(styles.btn)}>
                <Link to='wallet'>
                  <Button animated>
                    <Button.Content visible>Пополнить</Button.Content>
                    <Button.Content hidden>
                      <Icon name="plus" />
                    </Button.Content>
                  </Button>
                </Link>
              </div>
              <div className={classNames(styles.btn)}>
                <Link to='withdraw'>
                  <Button animated className={classNames(styles["btn-red"])}>
                    <Button.Content visible>Вывести</Button.Content>
                    <Button.Content hidden>
                      <Icon name="minus" />
                    </Button.Content>
                  </Button>
                </Link>
              </div>
            </div>
            )}
            
            <div className={classNames(styles["main-content"])}>
              <div className={classNames(styles.history)} style={{maxHeight: '100%', overflowX: 'scroll'}}>{+this.props.userStore.currentUser.id === 1 ? <AdminDashboard /> : <Withdraw />}</div>
            </div>
          </div>
        );
      }
    }
  )
);

const getBalance = () => {
  const {
    user: { email },
  } = deref(authAtom);

  const balance = localStorage.getItem("user@as.com");
  
  if (email === "user@as.com") {
    return <p>{balance ? balance : "0.00"}$</p>;
  } else {
    return <>0.00$</>;
  }
};

export { PersonalPage, getBalance };
