import React from "react";
import styles from "./security-data.module.scss";
import classNames from "classnames";
import { Password, RePassword } from "../../../sign-up/sub-components";
import { toast } from 'react-toastify';
import { inject, observer } from "mobx-react";

const SecurityData = inject('userStore')(
    observer(
      class SecurityData extends React.Component<any> {
        state = {
          oldPass: '',
          newPass: '',
          newRePass: ''
        }

        handleOldPassChange = (e:any) => this.setState({oldPass: e.target.value});
        handleNewPassChange = (e:any) => this.setState({newPass: e.target.value});
        handleNewRePassChange = (e:any) => this.setState({newRePass: e.target.value});

        handlePasswordChange = (e:any) => {
          if(this.state.newPass !== this.state.newRePass)
          {
            toast.error('Пароли не совпадают');
            return;
          }
          this.props.userStore.changePassword(this.state.oldPass, this.state.newPass);
        }

        render() {
          return (
            <div className={classNames(styles["settings"])}>
              <h1 className={classNames(styles.title)}>
                Смена пароля от кабинета
              </h1>
              <div className={classNames(styles.input)}>
                <Password title="Старый пароль" value={this.state.oldPass} onChange={this.handleOldPassChange}/>
              </div>
              <div className={classNames(styles.input)}>
                <Password title="Новый пароль" value={this.state.newPass} onChange={this.handleNewPassChange}/>
              </div>
              <div className={classNames(styles.input)}>
                <RePassword title="Повторите пароль" value={this.state.newRePass} onChange={this.handleNewRePassChange}/>
              </div>
              <div className={classNames(styles["btn-block"])}>
                <button className={classNames(styles.btn)} onClick={this.handlePasswordChange}>Сохранить</button>
              </div>
            </div>
          );
        }
      }
    )
);



export { SecurityData };
