import React, { useState, useEffect } from "react";
import "./App.scss";
import { Switch, Route } from "react-router-dom";
import { WalletPage, Profile } from "./containers";
import { Header, VerticalSidebar, PersonalPage, Deposits, Chart } from "./components";
import { Sidebar, Segment } from "semantic-ui-react";
import { useAtom } from "@dbeining/react-atom";
import { authAtom, signIn } from "./store";
import classNames from "classnames";
import { WalletWithdrawPage } from "./containers/wallet-withdraw-page";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { inject, observer } from "mobx-react";
import styles from "./components/header/header.module.scss";
import agent from './stores/agent';

import { SignInned as HeaderSignIn } from "./components/header/sub-components";
import { SignOuted as HeaderSignOut } from "./components/header/sub-components";

import Auth from './pages/Auth';
import Main from './pages/Main';
import { Redirect } from 'react-router-dom';

const App = inject('commonStore', 'userStore', 'packageStore', 'authStore')(
  observer(
    class App extends React.Component<any> {

      componentWillMount() {
        
        this.props.commonStore.getCurrenciesCourses();
        if (!this.props.commonStore.token) {
          this.props.commonStore.setAppLoaded();
        }
      }
    
      async componentDidMount() {
        if(!this.props.commonStore.activeCurrency)
        {
          this.props.commonStore.setActiveCurrency('$', true);
        }
        if (this.props.commonStore.token) {
          this.props.userStore.pullUser()
            .finally(() => this.props.commonStore.setAppLoaded());
        }
        this.props.packageStore.loadPackages();
      }

      render() {

        if (this.props.authStore.toDashboard === true) {
          this.props.authStore.disableToDashboard();
          return <Redirect to='/personal-page' />
        }
        if (this.props.commonStore.appLoaded) {
          return (
            <div className="App">
              <ToastContainer position={toast.POSITION.BOTTOM_RIGHT} />
                {(this.props.userStore.currentUser.id == 0 || this.props.userStore.currentUser == null) ? null : (<HeaderSignIn/>)}
              <Route component={(this.props.userStore.currentUser.id == 0 || this.props.userStore.currentUser == null) ? Auth : Main} />
            </div>
          );
        }
        return (
            <div>
              Loading...
            </div>
        );
      }
      
    }
  )
)




export default App;
