import React, { useState, useEffect } from "react";
import Iframe from "react-iframe";
import { Spinner } from "react-bootstrap";

interface IExchangeIFrameProps {
  height: string;
  width: string;
}

const ExchangeIFrame: React.FC<IExchangeIFrameProps> = ({ height, width }) => {
  const [loading, setLoading] = useState(true);

  const heightStyle = {
    height: `${height}`,
    width: `${width}`,
  };

  const styleWrap = {
    width: "100%",
    height: "100%",
    margin: "0 auto",
    padding: "0",
  };

  const style = {
    width: "100%",
    height: "100%",
    background: "transparent",
    padding: "0",
  };

  return (
    <div style={heightStyle}>
      <div style={styleWrap}>
        <div style={style}>
          <Iframe
            url={IFRAME_LINK}
            width="100%"
            height="100%"
            id="kuna"
            display={"block"}
            position="relative"
            frameBorder={0}
          />
        </div>
      </div>
    </div>
  );
};

