import React from "react";
import styles from "./document-verification.module.scss";
import classNames from "classnames";
import { Icon } from "semantic-ui-react";
import ImageUploader from 'react-images-upload';

import { inject, observer } from "mobx-react";

const ImageUpload = inject('userStore')(
  observer(
    class ImageUpload extends React.Component<any, any> {
      constructor(props:any) {
        super(props);
        this.state = {file: '',imagePreviewUrl: this.props.userStore.passport};
      }

      _handleSubmit(e:any) {
        e.preventDefault();
        console.log('handle uploading-');
        console.log(this.state.imagePreviewUrl);
      }

      _handleImageChange(e:any) {
        e.preventDefault();

        let reader = new FileReader();
        let file = e.target.files[0];

        reader.onloadend = () => {
          this.setState({
            file: file,
            imagePreviewUrl: reader.result
          });
          this.props.userStore.setPassport(reader.result);
        }

        // console.log(reader.result);

        reader.readAsDataURL(file)
      }

      

      render() {
        let {imagePreviewUrl} = this.state;
        let $imagePreview = null;
        if (imagePreviewUrl) {
          $imagePreview = (<img className={classNames(styles["image"])} src={imagePreviewUrl} />);
        } else {
          $imagePreview = (<React.Fragment>
<input
            type="file"
            id='document1'
            accept="image/x-png"
            style={{display: 'none'}}
            onChange={(e)=>this._handleImageChange(e)} />
<button className={classNames(styles.btn)} onClick={() => {
                document.getElementById('document1')?.click();
              }}>
              <Icon name="cloud download" />
              Загрузить документ
              </button>
            </React.Fragment>);
        }

        return (
          <div className="fileContainer">
              {$imagePreview}
          </div>
        )
      }
    }
  )
)



