const DocumentVerification = inject('userStore')(
  observer(
    class DocumentVerification extends React.Component<any, any> {


      // state = {
      //   files: []
      // }

      render() {
        return (
          <div className={classNames(styles["settings"])} >
            <h1 className={classNames(styles.title)}>
              Верификация документов
            </h1>
            <div className={classNames(styles["verifiction-item"])}>
              <h2 className={classNames(styles.title)}>Документы, подтверждающие личность</h2>
              <p className={classNames(styles.description)}>
                Фото паспорта, загран паспорта или водительского удостоверения
              </p>
              <div className={classNames(styles["btn-block"])}>
                <ImageUpload/>
                {/* <button className={classNames(styles.btn)}>
                  <Icon name="cloud download" />
                  Загрузить документ
                </button> */}
              </div>
            </div>
            <div className={classNames(styles["verifiction-item"])}>
              <h2 className={classNames(styles.title)}>Документы, подтверждающие адрес</h2>
              <p className={classNames(styles.description)}>
                Скан или фото платежки по ЖКХ или другого документа, подтверждающего адрес прописки.
              </p>
              <div className={classNames(styles["btn-block"])}>
                <ImageUpload2/>
              </div>
            </div>
            <div className={classNames(styles["verifiction-item"])}>
              <h2 className={classNames(styles.title)}>При необходимости:</h2>
              <p className={classNames(styles.description)}>Фото Вас с паспортом</p>
              <div className={classNames(styles["btn-block"])}>
                <ImageUpload3/>
              </div>
            </div>
          </div>
        );
      }
    }
  )
);

export { DocumentVerification };
