import React from "react";
import { Switch, Route } from "react-router-dom";
import { WalletPage, Profile } from "../../containers";
import { PersonalPage, Deposits, Chart, VerticalSidebar } from "../../components";
import { WalletWithdrawPage } from "../../containers/wallet-withdraw-page";
import { Sidebar, Segment } from "semantic-ui-react";
import classNames from "classnames";
import { inject, observer } from "mobx-react";

const Main = inject('commonStore', 'authStore')(
    observer(
        class Main extends React.Component<any> {
            
            state = {
                sidebarVisible: false,
            }

            componentDidMount() {
                this.props.history.replace('/personal-page');
                if(document.body.clientWidth < 700)
                {
                    this.setState({sidebarVisible: true});
                    this.props.commonStore.setSidebarVisible(this.state.sidebarVisible);
                }
            }
            
            toggleSidebar = () => {
                this.setState({sidebarVisible: !this.state.sidebarVisible});
                this.props.commonStore.setSidebarVisible(this.state.sidebarVisible);
            };

            render() {
                return (
                    <React.Fragment>
                        <VerticalSidebar visible={this.state.sidebarVisible} toggleSidebar={this.toggleSidebar} token={this.props.commonStore.token} />
                        <Sidebar.Pushable as={Segment} style={{ margin: 0, paddingTop: "96px" }}>
                            <Sidebar.Pusher
                                style={this.state.sidebarVisible ? { paddingLeft: "55px" } : { paddingLeft: "150px" }}
                                className={classNames("pusher")}
                            >
                                <Segment basic className={classNames("segment")}>
                                    <Switch>
                                        <Route path="/personal-page" component={PersonalPage} />
                                        <Route path="/deposits" component={() => <Deposits namePage="deposits" />} />
                                        <Route path="/robots" component={() => <Deposits namePage="robots" />} />
                                        <Route path="/profile" component={Profile} />
                                        <Route path="/security" component={Profile} />
                                        <Route path="/verification" component={Profile} />
                                        <Route path="/wallet" component={WalletPage} />
                                        <Route path="/withdraw" component={WalletWithdrawPage} />
                                        <Route path="/chart" component={Chart} />
                                    </Switch>
                                </Segment>
                            </Sidebar.Pusher>
                        </Sidebar.Pushable>
                    </React.Fragment>
                );
            }
        }
    )
);

export default Main;
