import React from "react";
import styles from "../../../deposits/deposit.module.scss";
import classNames from 'classnames';
import { Input, Icon } from "semantic-ui-react";

interface ISummSettingsProps {
  investmentSumm: number;
}

const SummSettings: React.FC<ISummSettingsProps> = ({investmentSumm}) => {
  return (
    <div className={classNames(styles["summ-settings"])}>
      <div>
        <h3>Укажите сумму инвестирования и валюту:</h3>
      </div>
      <div className={classNames(styles["input-summ"])}>
        <Input value={investmentSumm} focus={true} />
      </div>
      {/* <div className={classNames(styles.currency)}>
        <Icon name="dollar" className={classNames(styles.icon)} />
        <Icon name="ruble" className={classNames(styles.icon)} />
      </div> */}
    </div>
  );
};

export { SummSettings };
