import React from "react";
import styles from "./personal-data.module.scss";
import stylesSignUpData from "../../../sign-up/sign-up.module.scss";
import stylesPersonalData from "./personal-data.module.scss";
import classNames from "classnames";
import { Input } from "semantic-ui-react";
import { inject, observer } from "mobx-react";

const PersonalData = inject('userStore')(
    observer(
        class PersonalData extends React.Component<any> {

          render() {
            const { currentUserProfile, currentUser } = this.props.userStore;
            return (
              <div className={classNames(styles["settings"])}>
                <h1 className={classNames(styles.title)}>Редактирование персональных данных</h1>
                <div className={classNames(styles.input)}>
                  <Input type="text" placeholder="Имя" value={currentUserProfile.name} onChange={(e) => this.props.userStore.setUserProfilePropetry('name', e.target.value)}/>
                </div>
                <div className={classNames(styles.input)}>
                  <Input type="text" placeholder="Фамилия" value={currentUserProfile.surname} onChange={(e) => this.props.userStore.setUserProfilePropetry('surname', e.target.value)}/>
                </div>
                <div className={classNames(styles.input)}>
                  <Input type="text" placeholder="Отчество" value={currentUserProfile.fatherName} onChange={(e) => this.props.userStore.setUserProfilePropetry('fatherName', e.target.value)}/>
                </div>
                <div className={classNames(styles.input)}>
                  <Input type="text" placeholder="Дата рождения" value={currentUserProfile.birthDate} onChange={(e) => this.props.userStore.setUserProfilePropetry('birthDate', e.target.value)}/>
                </div>
                <div className={classNames(styles.input)}>
                  <Input type="text" placeholder="Email" disabled={true} value={currentUser.email}/>
                </div>
                <div className={classNames(styles.input)}>
                  <Input type="text" placeholder="Номер телефона" value={currentUserProfile.phone} onChange={(e) => this.props.userStore.setUserProfilePropetry('phone', e.target.value)}/>
                </div>
                <div className={classNames(styles.input)}>
                  <Input type="text" placeholder="Никнейм" disabled={true} value={currentUser.username}/>
                </div>
                <div className={classNames(styles["btn-block"])}>
                  <button className={classNames(styles.btn)} onClick={() => this.props.userStore.saveProfile(this.props.userStore.currentUserProfile)}>Сохранить</button>
                </div>
              </div>
            );
          }
        }
    )
);


// const PersonalData: React.FC<IPersonalDataProps> = props => {
  
// };

export { PersonalData };
