import { observable, action, reaction, decorate, computed } from 'mobx';
import agent from './agent';
import { toast } from 'react-toastify';

class CommonStore {

  appName = 'AirSolutions';
  token = window.localStorage.getItem('jwt');
  sidebarVisible = window.localStorage.getItem('sidebarVisible') || true;
  appLoaded = false;
  withdrawModalVisible = false;
  activeCurrency = window.localStorage.getItem('activeCurrency');
  USDtoRUB = 1;
  USDtoEURO = 1;
  EUROtoRUB = 1;
  activePaymentCard = false;
  activePaymentName = '';

  constructor() {
    reaction(
      () => this.token,
      token => {
        if (token) {
          window.localStorage.setItem('jwt', token);
        } else {
          window.localStorage.removeItem('jwt');
        }
      }
    );
    reaction(
      () => this.activeCurrency,
      activeCurrency => {
        if (activeCurrency) {
          window.localStorage.setItem('activeCurrency', activeCurrency);
        } else {
          window.localStorage.removeItem('activeCurrency');
        }
      }
    );
    reaction(
      () => this.sidebarVisible,
      sidebarVisible => {
        if (sidebarVisible) {
          window.localStorage.setItem('sidebarVisible', sidebarVisible);
        } else {
          window.localStorage.removeItem('sidebarVisible');
        }
      }
    );
  }

  setSidebarVisible(sidebarVisible)
  {
    this.sidebarVisible = sidebarVisible;
  }

  setWithdrawModalVisible(withdrawModalVisible)
  {
    this.withdrawModalVisible = withdrawModalVisible;
  }

  get activeCurrencyMultiplier() {
    const cms = {
      '$': 1,
      '₽': this.USDtoRUB,
      '€': this.USDtoEURO
    };
    return cms[this.activeCurrency];
  }

  get activeCurrencyIcon() {
    const cms = {
      '$': 'dollar',
      '₽': 'ruble',
      '€': 'euro'
    };
    return cms[this.activeCurrency];
  }

  get activeCurrencyMultiplierReverse() {
    const cms = {
      '$': 1,
      '₽': 1/this.USDtoRUB,
      '€': 1/this.USDtoEURO
    };
    return cms[this.activeCurrency];
  }

  setToken(token) {
    this.token = token;
  }

  setAppLoaded() {
    this.appLoaded = true;
  }

  async getCurrenciesCourses() {
    const response = await fetch('https://www.cbr-xml-daily.ru/daily_json.js')
    const responseJson = await response.json();
    this.USDtoRUB = responseJson.Valute.USD.Value;
    const EUROtoRUB = responseJson.Valute.EUR.Value;
    this.USDtoEURO = this.USDtoRUB / EUROtoRUB;
  }

  setActiveCurrency(currency, noCallback = false) {
    this.activeCurrency = currency;
    if(!noCallback)
    {
      toast.info('Основная валюта: ' + currency);
    }
    // alert(this.activeCurrency);
  }

  setActivePaymentCard()
  {
    this.activePaymentCard = true;
  }

  setActivePaymentName(name)
  {
    this.activePaymentName = name;
  }

  setInActivePaymentCard()
  {
    this.activePaymentCard = false;
  }

  

}

decorate(CommonStore, {
  activePaymentCard: observable,
  activePaymentName: observable,
  appName: observable,
  token: observable,
  sidebarVisible: observable,
  appLoaded: observable,
  activeCurrencyIcon: computed,
  USDtoEURO: observable,
  EUROtoRUB: observable,
  activeCurrency: observable,
  activeCurrencyMultiplierReverse: computed,
  activeCurrencyMultiplier: computed,
  USDtoRUB: observable,
  setToken: action,
  setAppLoaded: action,
  setActiveCurrency: action,
  setSidebarVisible: action,
  withdrawModalVisible: observable,
  setWithdrawModalVisible: action,
  setActivePaymentCard: action,
  setInActivePaymentCard: action,
  setActivePaymentName: action,
});

export default new CommonStore();
