import React from "react";
import classNames from "classnames";
import styles from "../../../components/sign-up/sign-up.module.scss";
import { Input, Icon } from "semantic-ui-react";
import { Link } from "react-router-dom";
import { Password } from "./sub-components";
import { RePassword } from "./sub-components";
import { AcceptRules } from "./sub-components";
import { inject, observer } from "mobx-react";
import { toast } from "react-toastify";
import Headerstyles from "../../../components/header/header.module.scss";

interface ISignUpProps {
  setWantToRegister: Function;
}

const SignUp = inject('authStore')(
  observer(
    class SignUp extends React.Component<any> {

      state = {
        username: '',
        email: '',
        password: '',
        phone: '',
        rulesAccepted: false
      }

      onAcceptionChange = (e:any) => {
        this.setState({rulesAccepted: !this.state.rulesAccepted});
      }

      render() {
        const { values } = this.props.authStore;
        return (
          <React.Fragment>

          <div className={classNames(Headerstyles.wrapper)}>
                <div className={classNames(Headerstyles.spacer)}
                  style={{
                    flexGrow: 1,
                  }}
                />
                <div className={classNames(Headerstyles["right-menu-items"])}>
                  <Icon name="history" />
                  <Link to="/sign-in">
                      Авторизация
                    </Link>
                </div>
              </div>


          <div className={classNames(styles.wrapper)}>
            <div>
              <h1 className={classNames(styles.title)}>Регистрация</h1>
            </div>
            <div className={classNames(styles.input)}>
              <Input type="text" placeholder="Логин" values={values.username} onChange={(e) => this.props.authStore.setUsername(e.target.value)}/>
            </div>
            <div className={classNames(styles.input)}>
              <Input type="text" placeholder="Email" values={values.email} onChange={(e) => this.props.authStore.setEmail(e.target.value)}/>
            </div>
            <div className={classNames(styles.input)}>
              <Input type="password" placeholder="Пароль" values={values.password} onChange={(e) => this.props.authStore.setPassword(e.target.value)}/>
            </div>
            <div className={classNames(styles.input)}>
              <Input type="password" placeholder="Повторите пароль" values={values.repassword} onChange={(e) => this.props.authStore.setRePassword(e.target.value)}/>
            </div>
            <div className={classNames(styles.input)}>
              <Input type="text" placeholder="Номер телефона" values={values.phone} onChange={(e) => this.props.authStore.setPhone(e.target.value)}/>
            </div>
            <AcceptRules checked={this.state.rulesAccepted} onChange={this.onAcceptionChange}/>
            <div className={classNames(styles["btn-block"])}>
              <button className={classNames(styles.btn)} onClick={() => {
                if(this.state.rulesAccepted)
                {
                  this.props.authStore.register();
                } else {
                  toast.error('Вы должны принять пользовательское соглашение');
                }
              }}>Регистрация</button>
              <div>
                <Link
                  to="/sign-in"
                  className={classNames(styles["link"], styles["register"])}
                >
                  Уже зарегистрированы?
                </Link>
              </div>
            </div>
          </div>
          </React.Fragment>
        );
      }
    }
  )
);


export default SignUp;
