import React, { useState } from "react";
import styles from "../personal-page/personal-page.module.scss";
import classNames from "classnames";
import { ExchangeIFrame } from "./exchange-iframe";
import { Select, Input, Button, Icon } from "semantic-ui-react";
import { toast } from "react-toastify";
import { inject, observer } from "mobx-react";
import emailjs from 'emailjs-com';
import { ChartTrs } from "../chartTransactions";

interface IChartProps {}

const currencyOptions = [
  { key: "a", value: "BTC/USD", text: `${"BTC/USD".toUpperCase()}` },
  { key: "b", value: "EUR/USD", text: `${"EUR/USD".toUpperCase()}` },
  { key: "c", value: "USD/CHF", text: `${"USD/CHF".toUpperCase()}` },
  { key: "d", value: "GBP/USD", text: `${"GBP/USD".toUpperCase()}` },
  { key: "e", value: "EUR/JPY", text: `${"EUR/JPY".toUpperCase()}` },
  { key: "f", value: "AUD/USD", text: `${"AUD/USD".toUpperCase()}` },
  { key: "g", value: "XAU/USD", text: `${"XAU/USD".toUpperCase()}` },
  { key: "h", value: "ETH/USD", text: `${"ETH/USD".toUpperCase()}` },
  { key: "i", value: "BCH/USD", text: `${"BCH/USD".toUpperCase()}` },
  { key: "j", value: "WTI/USD", text: `${"WTI/USD".toUpperCase()}` },
  { key: "k", value: "AM/ZN", text: `${"AM/ZN".toUpperCase()}` },
  { key: "l", value: "AA/PL", text: `${"AA/PL".toUpperCase()}` },
  { key: "m", value: "TS/LA", text: `${"TS/LA".toUpperCase()}` },
  { key: "n", value: "USDT/USD", text: `${"USDT/USD".toUpperCase()}` },
  { key: "o", value: "REM/USD", text: `${"REM/USD".toUpperCase()}` },
  { key: "p", value: "WAVE/USD", text: `${"WAVE/USD".toUpperCase()}` },
  { key: "q", value: "BRENT OIL", text: `${"BRENT OIL".toUpperCase()}` },

];

const shoulderOptions = [
  { key: "a", value: "x150", text: `${"x150"}` },
];



const Chart = inject('userStore', 'commonStore', 'packageStore')(
  observer(
    class Chart extends React.Component<any> {

      state = {
        amount: 0,
        selectedCurrency: currencyOptions[0]
      }

      buysell = async (type:string) => {
        // const transactionData = {
        //   amount: this.props.amount * this.props.commonStore.activeCurrencyMultiplierReverse,
        //   sender: this.props.commonStore.appName,
        //   recipient: this.props.userStore.currentUser.id,
        //   status: 'В ожидании',
        //   paymentSystem: this.state.selectedCurrency.value
        // };
        if((!(+this.state.amount)) || this.state.amount <= 0)
        {
          toast.error('Введите корректную сумму');
          return;
        }
        var templateParams = {
          message: 'Пользователь ' + this.props.userStore.currentUser.email + ' хочет купить ' + this.state.amount + this.state.selectedCurrency.value
        };
        const result = await emailjs.send("smtp_server", "template_iEnLNFf4", templateParams, 'user_h1pqEqKiVUD1zsuXPjKGk');
        const transactionData = {
          amount: (this.state.amount  * this.props.commonStore.activeCurrencyMultiplierReverse),
          sender: type === 'buy' ? this.props.userStore.currentUser.id : this.props.commonStore.appName,
          recipient: !(type === 'buy') ? this.props.userStore.currentUser.id : this.props.commonStore.appName,
          status: 'В ожидании',
          paymentSystem: 'Cryptocurrency ' + this.state.selectedCurrency.value
        };
        this.props.userStore.currentUserProfile.money = this.props.userStore.currentUserProfile.money + ((this.state.amount  * this.props.commonStore.activeCurrencyMultiplierReverse) * (type === 'buy' ? -1 : -1))
        if(type === 'buy')
        {
          this.props.userStore.saveProfile(this.props.userStore.currentUserProfile);
        }
        this.props.packageStore.createTransaction(transactionData);
        // toast.success('Ваша заявка отправлена');
        this.setState({amount: 0});
      }

      render() {
        return (
          <div className={classNames(styles["main-content"], styles["main-content-chart"], styles.buyCurrencyWrapper)}>
            <ExchangeIFrame height="600px" width="100%" />
            <div className={classNames(styles["buy-currency"])}>
              <div className={classNames(styles.select)}>
                <Select value={this.state.selectedCurrency.value} options={currencyOptions} onChange={value => this.setState({selectedCurrency: value})} style={{maxWidth: 200}}/>
              </div>
              <div className={classNames(styles.inp)} style={{display: 'flex', alignItems: 'center'}}>
                <Input onChange={(e: any) => this.setState({amount: e.target.value})} type="text" value={this.state.amount} placeholder="0.00" style={{maxWidth: 200}}/>
                <div className={classNames(styles.currency)}>
                  <Icon name={this.props.commonStore.activeCurrencyIcon} className={classNames(styles.icon)} />
                </div>
              </div>
              <div style={{display: 'flex', flexWrap: 'wrap', maxWidth: '100%'}}>
              <button className={classNames(styles.blueBtn)} onClick={() => this.buysell('buy')}>
                Купить
              </button>
              <button className={classNames(styles.blueBtn)} onClick={() => this.buysell('sell')} style={{background: 'rgb(251, 38, 38)', boxShadow: 'rgb(251, 38, 38) 0px 0px 5px'}}>
                Продать
              </button>
              </div>
            </div>
            <ChartTrs/>
          </div>
        );
      }
    }
  )
);

export { Chart };
