import React from "react";
import styles from "../../../deposits/deposit.module.scss";
import classNames from "classnames";
import { IPlan } from '../../../../types/plan.type';

interface IProfitPercentProps {
  plan: IPlan,
  page: string
}

const ProfitPercent: React.FC<IProfitPercentProps> = ({plan, page}) => {
  return (
    <div className={classNames(styles["profit-percent"])}>
      <h2>
        {plan["name"]} {plan["percent"]}% {page === 'robots' ? null : (<span>в сутки</span>)}
      </h2>
      <p>Срок контракта: {plan["terms"]} дней</p>
      <p>Одноразовый депозит не требующий верификации</p>
    </div>
  );
};

export { ProfitPercent };
