import React from "react";
import styles from "../../../deposits/deposit.module.scss";
import classNames from "classnames";
import { IPlan } from "../../../../types/plan.type";
import { inject, observer } from "mobx-react";

interface IProfitInPeriodProps {
  currentCardData: IPlan;
  investmentSumm: number;
}

const PofitInPeriod = inject('commonStore')(
  observer(
    class PofitInPeriod extends React.Component<any> {
      render() {
        // alert(this.props.investmentSumm);
        return (
          <div className={classNames(styles["profit-in-period"])}>
            <div>
              <h1 className={classNames(styles["deposit-money"])}>{this.props.commonStore.activeCurrency} {(this.props.investmentSumm * this.props.currentCardData.coefficient).toFixed(2)}</h1>
              <p className={classNames(styles.profit)}>
                Ваша прибыль в <span className={classNames(styles.period)}>день</span>
              </p>
            </div>
            {/*<div>*/}
            {/*  <h1 className={classNames(styles["deposit-money"])}>{this.props.commonStore.activeCurrency} {(this.props.investmentSumm * this.props.currentCardData.coefficient * 30).toFixed(2)}</h1>*/}
            {/*  <p className={classNames(styles.profit)}>*/}
            {/*    Ваша прибыль за <span className={classNames(styles.period)}>месяц</span>*/}
            {/*  </p>*/}
            {/*</div>*/}
            <div>
              <h1 className={classNames(styles["deposit-money"])}>
              {this.props.commonStore.activeCurrency} {(this.props.investmentSumm * this.props.currentCardData.coefficient * this.props.currentCardData.terms).toFixed(2)}
              </h1>
              <p className={classNames(styles.profit)}>
                Ваша прибыль за <span className={classNames(styles.period)}>весь период</span>
              </p>
            </div>
          </div>
        );
      }
    }
  )
);

export { PofitInPeriod };
