import React from "react";
import styles from "../../../deposits/deposit.module.scss";
import classNames from 'classnames';
import { ProfitPercent } from "./profit-percent";
import { PofitInPeriod } from "./profit-in-period";
import { Invest } from "./invest";
import { IPlan } from '../../../../types/plan.type';

interface IProfitsProps {
  currentCardData: IPlan;
  investmentSumm: number;
  page: string;
}

const Profits: React.FC<IProfitsProps> = ( { currentCardData, investmentSumm, page } ) => {
  return (
    <div className={classNames(styles.profits)}>
      <ProfitPercent plan={currentCardData} page={page} />
      <PofitInPeriod currentCardData={currentCardData} investmentSumm={investmentSumm} />
      <Invest currentCardData={currentCardData} investmentSumm={investmentSumm} page={page}/>
    </div>
  );
};

export { Profits };
