import React from "react";
import styles from "./withdraw.module.scss";
import classNames from "classnames";
import { TableContainer, TableHead } from "@material-ui/core";
import { Table, TableRow, TableCell, TableBody } from "semantic-ui-react";
import { inject, observer } from "mobx-react";
import moment from 'moment';

interface IWithdrawProps {}


const ChartTrs= inject('packageStore', 'userStore', 'commonStore')(
  observer(
    class Withdraw extends React.Component<any> {

      componentWillMount() {
        this.props.packageStore.loadMyTransactions();
      }

      render() {
        const { myTransactions } = this.props.packageStore;
        const { activeCurrencyMultiplier, activeCurrency } = this.props.commonStore;
        return (
          <div className={classNames(styles.wrapper)}>
            <h1 className={classNames(styles.title)}>История сделок:</h1>
            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Действие</TableCell>
                    <TableCell>Дата</TableCell>
                    <TableCell>Сумма</TableCell>
                    <TableCell>Платежная система</TableCell>
                    <TableCell>Номер транзакции</TableCell>
                    <TableCell>Статус</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {myTransactions.map((transaction:any) => {
                    if(! String(transaction.paymentSystem).includes('Cryptocurrency'))
                    {
                      return <React.Fragment></React.Fragment>
                    }
                    return (
                      <TableRow key={transaction.id}>
                        <TableCell component="th" scope="row">
                          {String(transaction.sender) === String(this.props.userStore.currentUser.id) ? 'Покупка' : 'Продажа'}
                        </TableCell>
                        <TableCell align="right">{moment(transaction.date).format('YYYY-MM-DD HH:mm:ss')}</TableCell>
                        <TableCell align="right">{(transaction.amount * activeCurrencyMultiplier).toFixed(2)}{activeCurrency}</TableCell>
                        <TableCell align="right">{transaction.paymentSystem}</TableCell>
                        <TableCell align="right">{419925 + transaction.id}</TableCell>
                        <TableCell align="right" className={transaction.status === 'Обработано' ? classNames(styles.success) : transaction.status === 'Отклонено' ? classNames(styles.danger) : classNames(styles.warning)}>
                          {transaction.status}
                        </TableCell>
                      </TableRow>
                    );
                  })}
                </TableBody>
              </Table>
            </TableContainer>
          </div>
        );
      }
    }
  )
);


export { ChartTrs };
